let util = {}

util.getCenterByTwoPoint = function (p1, p2) {
  let x = (p1.x + p2.x) / 2
  let y = (p1.y + p2.y) / 2
  return {x, y}
}

util.dateToString = function (dateTime, format) {
  // 时间格式化
  var d, k, o
  let a = dateTime instanceof Date
  if (!a) dateTime = new Date(dateTime)
  o = {
    'M+': dateTime.getMonth() + 1,
    'd+': dateTime.getDate(),
    'h+': dateTime.getHours(),
    'm+': dateTime.getMinutes(),
    's+': dateTime.getSeconds(),
    'q+': Math.floor((dateTime.getMonth() + 3) / 3),
    S: dateTime.getMilliseconds()
  }
  if (/(y+)/.test(format)) {
    format = format.replace(
      RegExp.$1,
      (dateTime.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (k in o) {
    d = o[k]
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? d : ('00' + d).substr(('' + d).length)
      )
    }
  }
  return format
}

export default util



