<template>
  <div style="height: 100%;">
    <!--<div class="header-nav" v-if="inConfigurationPath">
      <router-link to="/">
        <span>五子棋</span>
      </router-link>
      <router-link to="/language">
        <span>地址识别</span>
      </router-link>
      <router-link to="/svg">
        <span>编辑组态</span>
      </router-link>
    </div>-->
    <el-menu
      v-if="inConfigurationPath"
      default-active="/"
      mode="horizontal"
      class="el-menu-vertical-demo my-menu"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      router>
      <el-menu-item index="/" :route="{path: '/'}">
        <i class="el-icon-menu"></i>
        <span slot="title">五子棋</span>
      </el-menu-item>
      <el-menu-item index="/language" :route="{path: '/language'}">
        <i class="el-icon-location"></i>
        <span slot="title">地址识别</span>
      </el-menu-item>
      <el-menu-item index="/svg">
        <i class="el-icon-setting"></i>
        <span slot="title">编辑组态</span>
      </el-menu-item>
      <el-menu-item index="/cssTest">
        <i class="el-icon-edit"></i>
        <span slot="title">css测试</span>
      </el-menu-item>
    </el-menu>
    <el-row class="cg-close__btn" v-if="!inConfigurationPath">
      <i class="el-icon-close" @click="goTogomoku"></i>
    </el-row>
    <div id="home" style="min-height: 600px; height: calc(100% - 61px); position: relative; top: 61px;">
      <router-view :offset='offset'></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      configurationPath: '/svg',
      inConfigurationPath: true,
      offset: {
        top: 61,
        left: 0
      }
    }
  },
  updated () {
    const currentRoute = this.$route
    const curPath = currentRoute.path
    if (this.currentRouter !== curPath) { // 路由改变
      this.currentRouter = curPath
      // if (curPath === this.configurationPath || curPath === '/three') { // 哪个页面要默认全屏 curpath就设置等于什么
      if (curPath === '/three') { // 哪个页面要默认全屏 curpath就设置等于什么
        this.inConfigurationPath = false
      } else {
        this.inConfigurationPath = true
      }
    }
  },
  methods: {
    goTogomoku () {
      this.$router.go(-1)
    }
  }
}
</script>

<style>
.header-nav {
  height: 30px;
  line-height: 30px;
  font-size: 16px;
}
.cg-close__btn {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 11111111;
}
a {
  text-decoration:none;
}
.my-menu {
  z-index: 1111;
  position: fixed;
  width: 100%;
  top: 0;
}
</style>



