<template>
<svg :y="Number(index) * 80" width="90" height="90" @click="getGrap">
  <ellipse v-if="myGraps.ecs.length > 0" v-for="(ec, index) in myGraps.ecs" :key="'myGrapEc' + index"
    :cx="ec.svgConfig.cx" :cy="ec.svgConfig.cy" :rx="ec.svgConfig.rx" :ry="ec.svgConfig.ry"
    class="my-svg" style="fill: #ccc;stroke-width:1; stroke:rgb(0,0,0);" />
  <path v-if="myGraps.pls.length > 0" v-for="(pl, index) in myGraps.pls" :d="pl.svgConfig.d"
    :key="'myGrapPl' + index"  fill="none" style="stroke: #333;stroke-width: 4" />
</svg>
</template>

<script>
import contour from '@/components/chartFlow/contour'
export default {
  components: {
    contour
  },
  props: {
    myGrap: {
      type: Array,
      require: true
    },
    offset: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data () {
    return {
      myGraps: {
        ecs: [],
        pls: []
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      for (let i = 0, l = this.myGrap.length; i < l; i++) {
        let item = this.myGrap[i]
        if (item.name === 'ec') {
          this.myGraps.ecs.push(item)
        } else if (item.name === 'pl') {
          this.myGraps.pls.push(item)
        }
      }
    })
  },
  methods: {
    getGrap () {
      this.$emit('on-click', JSON.stringify(this.myGraps))
    }
  }
}
</script>

<style>
</style>



