<template>
<el-dialog title="历史记录" :visible.sync="grid.visible">
  <el-table :data="grid.data">
    <el-table-column property="black" label="黑棋" width="150"></el-table-column>
    <el-table-column property="white" label="白棋" width="200"></el-table-column>
    <el-table-column property="saveTime" label="saveTime"></el-table-column>
    <el-table-column property="winType" label="结果"></el-table-column>
  </el-table>
</el-dialog>
</template>

<script>
export default {
  props: {
    grid: Object
  }
}
</script>

<style>
</style>



