import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    gomokuReplay: []
  },
  mutations: {
    initGomokuReplay (state, item) {
      state.gomokuReplay.push(item)
    },
    updateGomokuReplay (state, item) {
      state.gomokuReplay.push(item)
    }
  }
})

export default store



