<template>
<div style="text-align: left;display: flex;position: relative;">
  <div style="width: 99px; border-right: 1px solid #ccc;">
    <div>
      <svg v-if="mainSvg" width="100" height="60">
        <ellipse v-if="baseGrap.ec" id='elsvg' ref='mySvg' :cx="baseGrap.ec.svgConfig.cx"
          :cy="baseGrap.ec.svgConfig.cy" :rx="baseGrap.ec.svgConfig.rx" :ry="baseGrap.ec.svgConfig.ry"
          class="my-svg" style="fill: #ccc;stroke-width:1; stroke:rgb(0,0,0);"
          @click="getSvg($event, 0)" draggable="true" @dragstart="dragstart($event, baseGrap.ec)" />
      </svg>
    </div>
    <div>
      <svg v-if="mainSvg" width="100" height="60">
        <path v-if="baseGrap.pl" :d="baseGrap.pl.svgConfig.d"
          style="fill: white;stroke: #333;stroke-width: 4" ref="myPl"
          draggable="true" @dragstart="dragstart($event, baseGrap.pl)" />
          <!--<my-grap v-for='(myGrap, index) in myGrapList' :key="'myGrap' + index"
             :offset='offset' :index='index' :myGrap='myGrap'></my-grap>-->
      </svg>
    </div>
    <div>
      <my-grap-tool v-for='(myGrap, index) in myGrapList' :key="'myGrapTool' + index"
        :offset='offset' :index='index' :myGrap='myGrap' @on-click='getMyGrap'></my-grap-tool>
    </div>
  </div>
  <!--<svg v-if="mainSvg" version="1.1" height="1000" style="width: 100px;border: 1px solid #ccc;" id='sv'>
    <ellipse v-if="baseGrap.ec" id='elsvg' ref='mySvg' :cx="baseGrap.ec.svgConfig.cx"
      :cy="baseGrap.ec.svgConfig.cy" :rx="baseGrap.ec.svgConfig.rx" :ry="baseGrap.ec.svgConfig.ry"
      class="my-svg" style="fill: #ccc;stroke-width:1; stroke:rgb(0,0,0);"
      @click="getSvg($event, 0)" draggable="true" @dragstart="dragstart($event, baseGrap.ec)" />
    <path v-if="baseGrap.pl" :d="baseGrap.pl.svgConfig.d"
      style="fill: white;stroke: #333;stroke-width: 4" ref="myPl"
      draggable="true" @dragstart="dragstart($event, baseGrap.pl)" />
      <my-grap v-for='(myGrap, index) in myGrapList' :key="'myGrap' + index"
         :offset='offset' :index='index' :myGrap='myGrap'></my-grap>
  </svg>-->
  <div :style="baseGrap.ec.lrList[0].style" class="canvas__round"></div>
  <div :style="baseGrap.ec.lrList[1].style" class="canvas__round"></div>
  <div :style="baseGrap.ec.lrList[2].style" class="canvas__round"></div>
  <div style="width: 10px;"></div>
  <svg version="1.1" height="1000" style="width:100%;border: 1px solid #ccc;" @mousedown.self='isRangeSel'
    @click.self='clearSvg'>
  <!--<svg version="1.1" height="1000" style="width:100%;border: 1px solid #ccc;">-->
    <!--画图区域-->
    <svg @dragover="allowDrop" @drop="drop" @mousedown='isRangeSel' @click='clearSvg'>
      <rect width="800" height="800" :x="this.bgLineList.xs" :y="this.bgLineList.ys"
        style="fill: #fff;"/>
    </svg>
    <!--画图区域的网图-->
    <svg @dragover="allowDrop" @drop="drop" @mousedown='isRangeSel' @click='clearSvg'>
      <line v-for="ver in bgLineList.ver" :key="ver.verKey"
        :x1="ver.x1" :y1="ver.y1" :x2="ver.x2" :y2="ver.y2" :stroke='ver.stroke'
        style="stroke-width: 1"/>
      <line v-for="hor in bgLineList.hor" :key="hor.horKey"
        :x1="hor.x1" :y1="hor.y1" :x2="hor.x2" :y2="hor.y2" :stroke='hor.stroke'
        style="stroke-width: 1"/>
    </svg>
    <my-ellipse v-if="svgContentList.ec.length > 0"
      v-for="(ec, index) in svgContentList.ec" :key="ec.ref"
      :bgLineList='bgLineList' :client='client' :offset='offset' @moveAllSelGrap='moveAllSelGrap'
      @moveAllSelGrapEnd='moveAllSelGrapEnd'
      @movering='movering' @moverEnd='moverEnd' @setContourPoint='setContourPoint'
      :index='index' :ref='ec.ref' :grap="ec">
    </my-ellipse>
    <my-pl v-if="svgContentList.pl.length > 0"
      v-for="(pl, index) in svgContentList.pl" :key="pl.ref"
      :bgLineList='bgLineList' :client='client' :offset='offset' @moveAllSelGrap='moveAllSelGrap'
      @moveAllSelGrapEnd='moveAllSelGrapEnd'
      @movering='movering' @moverEnd='moverEnd' @setContourPoint='setContourPoint'
      :index='index' :ref='pl.ref' :grap="pl">
    </my-pl>
    <my-grap v-if="svgContentList.mg.length > 0" :ref='myGrap.ref' :client='client'
      v-for='(myGrap, index) in svgContentList.mg' :key="'myGrap' + index"
      :offset='offset' :index='index' :grap='myGrap' :bgLineList='bgLineList'>
    </my-grap>
    <g>
      <line v-for="(line, index) in promptLine" :key="index"
        :x1="line.x1" :y1="line.y1" :x2="line.x2" :y2="line.y2"
        style="stroke: #54ff00;stroke-width: 1"/>
    </g>
    <g v-for="con in contourList" :key="con.conKey" v-if="false">
      <rect :x="con.x" :y="con.y" :width="con.width" :height="con.height" fill="none"
        stroke="#00a8ff" stroke-dasharray="3 3" pointer-events="none"></rect>
      <g v-for="cir in con.cirList" :key="cir.cirKey">
        <image :x="cir.x" :y="cir.y" width="16" height="16" xlink:href="data:image/png;base64," preserveAspectRatio="none"></image>
      </g>
    </g>
  </svg>
  <div class="mxRubberband" v-show="rangeSel.show" :style="rangeSel.style"></div>
  <div style="position: absolute;top: 20px;left: 140px;" class="svg-btn__md" @click="saveToMySvg">保存</div>
  <!--<div>
    <div v-for="(lr, index) in svgContentList.ec.lrList" :key="index"
      @mousedown.prevent="lrMd($event, lr.type, lr.style)"
      :style="lr.style" class="canvas__round"></div>
  </div>-->
</div>
</template>

<script>
import myEllipse from '@/components/chartFlow/my-ellipse'
import myPl from '@/components/chartFlow/my-pl'
import myGrapTool from '@/components/chartFlow/my-grap-tool'
import myGrap from '@/components/chartFlow/my-grap'
export default {
  // TODO, 流程图里选中区域有问题
  components: {
    myEllipse,
    myPl,
    myGrapTool,
    myGrap
  },
  props: {
    offset: Object
  },
  data () {
    return {
      rangeSel: this.initRsStyle(),
      contourList: [], // 存放轮廓矩形
      bgLineList: { // 网格基本数据
        ver: [], // 网格竖线
        hor: [], // 网格横线
        ys: 8,
        ye: 808,
        xs: 208,
        xe: 1008
      },
      selectedGraps: [], // 被选中的grap
      basePoint: {
        pointList: [], // 存放各个点的基点
        bpRef: [], // 基准点对应的ref
        contourPointList: []
      },
      promptLine: [], // 存放对齐线
      isDraging: {}, // 工具栏中正在拖拽的项目
      svgContentList: {
        ec: [],
        pl: [],
        mg: []
      },
      mainSvg: true,
      // offset: {
      //   left: 0,
      //   top: 0
      // },
      client: { // 记录鼠标位置
        x: 0,
        y: 0
      },
      baseGrap: {
        ec: this.ecBaseInfo(),
        pl: this.plBaseInfo()
      },
      myGrapList: [] // 保存自定义的图形，它由多个图形组成
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.svgKey = new Date().getTime()
      for (let i = 0; i < 101; i++) { // 画网格
        let lc = 8 + i * 8
        let stroke = '#f2ebeb'
        if (i % 5 === 0) stroke = '#ccc'
        this.bgLineList.ver.push({
          x1: lc + 200,
          y1: this.bgLineList.ys,
          x2: lc + 200,
          y2: this.bgLineList.ye,
          verKey: this.svgKey++,
          stroke
        })
        this.bgLineList.hor.push({
          y1: lc,
          x1: this.bgLineList.xs,
          y2: lc,
          x2: this.bgLineList.xe,
          horKey: this.svgKey++,
          stroke
        })
      }
    })
  },
  methods: {
    // 保存，通过选择的组件的ref，获取组件的图形信息
    // 如何保存 （在右边兰的时候是同比缩小还是保存成图片）
    saveToMySvg () {
      console.log('保存')
      let myGrap = []
      for (let i = 0; i < this.selectedGraps.length; i++) {
        let newRef = this.selectedGraps[i]
        myGrap.push(JSON.parse(JSON.stringify(this.$refs[newRef][0].setGrapInfo())))
      }
      if (myGrap.length > 0) {
        // 寻找最上 最右 最下 最左四个点
        const fistP = [myGrap[0].lrList[0].style.cx, myGrap[0].lrList[0].style.cy]
        let points = [fistP, fistP, fistP, fistP] // 上右下左, 初始化四个点 points: [[x, y], [x, y], [x, y], [x, y]]
        for (let i = 0; i < myGrap.length; i++) {
          for (let j = 0; j < myGrap[i].lrList.length; j++) {
            let point = myGrap[i].lrList[j].style
            if (point.cy < points[0][1]) { // 更新最上点
              points[0] = [point.cx, point.cy]
            }
            if (point.cx > points[1][0]) { // 更新最右点
              points[1] = [point.cx, point.cy]
            }
            if (point.cy > points[2][1]) { // 更新最下点
              points[2] = [point.cx, point.cy]
            }
            if (point.cx < points[3][0]) { // 更新最下点
              points[3] = [point.cx, point.cy]
            }
          }
        }
        let xLen = points[1][0] - points[3][0] // x轴长度
        let ylen = points[2][1] - points[0][1] // y轴长度
        let maxLen = Math.max(xLen, ylen)
        let multiple = maxLen / 80
        console.log(multiple)
        // 再图形保存之前， 应该让他们平移到左上角
        // TODO,移动之后要同比缩小图片
        for (let i = 0; i < myGrap.length; i++) {
          if (myGrap[i].name === 'ec') {
            myGrap[i].svgConfig.cx -= (points[3][0] - 2)
            myGrap[i].svgConfig.cy -= (points[0][1] - 2)
            const rx = Number(myGrap[i].svgConfig.rx)
            const ry = Number(myGrap[i].svgConfig.ry)
            myGrap[i].svgConfig.rx = rx / multiple
            myGrap[i].svgConfig.cx = myGrap[i].svgConfig.cx / multiple
            myGrap[i].svgConfig.ry = ry / multiple
            myGrap[i].svgConfig.cy = myGrap[i].svgConfig.cy / multiple
          }
          if (myGrap[i].name === 'pl') {
            let dArr = myGrap[i].svgConfig.d.split(' ')
            for (let j = 0; j < dArr.length; j++) {
              if (j % 3 === 1) {
                dArr[j] = (Number(dArr[j]) - points[3][0] + 2) / multiple
              } else if (j % 3 === 2) {
                dArr[j] = (Number(dArr[j]) - points[0][1] + 2) / multiple
              }
            }
            myGrap[i].svgConfig.d = dArr.join(' ')
          }
        }
        console.log(myGrap)
        this.myGrapList.push(myGrap)
      }
    },
    initRsStyle () {
      return {
        show: false,
        style: {
          left: '0px',
          top: '0px',
          width: '0px',
          height: '0px'
        }
      }
    },
    ecBaseInfo () { // 椭圆基本信息
      return {
        name: 'ec',
        ref: 'ec',
        childRef: 'myEc',
        svgConfig: { // ec基础属性
          cx: 42,
          cy: 26,
          rx: 40,
          ry: 24
        },
        lrList: [{ // 存放锚点 (r: 右， l： 左， t： 上， b： 下) 顺序： 上右下左中
          show: true,
          type: 't',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }, {
          show: true,
          type: 'r',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }, {
          show: true,
          type: 'b',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }, {
          show: true,
          type: 'l',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }, {
          show: true,
          type: 'c',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }],
        svgSty: {
          left: '',
          top: ''
        },
        xs: false, // 小圆点是否显示
        ys: false,
        cs: false
      }
    },
    plBaseInfo () {
      return {
        name: 'pl',
        ref: 'polyline',
        childRef: 'myPl',
        svgConfig: { // ec基础属性
          d: 'M 2 20 L 80 20'
        },
        lrList: [{ // 存放锚点 (s: 头, e： 尾) 顺序： 头尾中
          show: true,
          type: 'f',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }, {
          show: true,
          type: 'c',
          style: {
            left: '',
            top: '',
            cx: 0,
            cy: 0
          }
        }]
      }
    },
    getMyGrap (grap) {
      let thisGrap = {}
      thisGrap.svgConfig = JSON.parse(grap)
      thisGrap.name = 'mg'
      thisGrap.ref = 'myGrap'
      thisGrap.childRef = 'mgCr'
      this.svgContentList.mg.push(thisGrap)
    },
    // 移动所有被选中图形_正在移动s
    moveAllSelGrap (disX, disY, selfRef) {
      for (var i = 0; i < this.selectedGraps.length; i++) {
        let newRef = this.selectedGraps[i]
        if (newRef === selfRef) {
          continue
        }
        let isfollow = true
        this.$refs[newRef][0].moveSvg(disX, disY, isfollow)
      }
    },
    // 移动所有被选中图形_已经停止
    moveAllSelGrapEnd (selfRef) {
      for (var i = 0; i < this.selectedGraps.length; i++) {
        let newRef = this.selectedGraps[i]
        if (newRef === selfRef) {
          continue
        }
        this.$refs[newRef][0].moverEnd()
      }
    },
    // 被选中的图形展现contour图形
    mulSelected () {
      for (var i = 0; i < this.basePoint.bpRef.length; i++) {
        let newRef = this.basePoint.bpRef[i]
        let index = this.selectedGraps.indexOf(newRef)
        if (index > -1) {
          this.$refs[newRef][0].showThisContour(true)
        } else {
          this.$refs[newRef][0].showThisContour(false)
        }
      }
    },
    // 找出哪些图形被选中
    selGraps (x1, y1, x2, y2) {
      let left1 = x1 - this.offset.left
      let left2 = x2 - this.offset.left
      let top1 = y1 - this.offset.top
      let top2 = y2 - this.offset.top
      this.selectedGraps = []
      for (var i = 0; i < this.basePoint.contourPointList.length; i++) {
        let contourP = this.basePoint.contourPointList[i]
        if (contourP.x > left1 && contourP.xMax < left2 && contourP.y > top1 && contourP.yMax < top2) {
          this.selectedGraps.push(this.basePoint.bpRef[i]) // 存放被选中的grap的ref
        }
      }
      this.mulSelected()
    },
    clearSvg () {
      for (var i = 0; i < this.basePoint.bpRef.length; i++) {
        let newRef = this.basePoint.bpRef[i]
        this.$refs[newRef][0].singleSel(false)
      }
    },
    // 拖动鼠标选择图形
    isRangeSel (e) {
      console.log('拖动鼠标')
      this.rangeSel.show = true
      const cx = e.clientX - this.offset.left
      const cy = e.clientY - this.offset.top
      let x1 = cx
      let y1 = cy
      let x2 = cx
      let y2 = cy
      this.selGraps(x1, y1, x2, y2)
      document.onmousemove = (e) => {
        let xend = e.clientX
        let yend = e.clientY
        if ((cx - xend) > 0) {
          x1 = xend
          x2 = cx
        } else {
          x1 = cx
          x2 = xend
        }
        if ((cy - yend) > 0) {
          y1 = yend
          y2 = cy
        } else {
          y1 = cy
          y2 = yend
        }
        this.rangeSel.style.left = this.addPx(x1)
        this.rangeSel.style.top = this.addPx(y1)
        this.rangeSel.style.width = this.addPx(Math.abs(cx - xend))
        this.rangeSel.style.height = this.addPx(Math.abs(cy - yend))
        this.selGraps(x1, y1, x2, y2)
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
        this.rangeSel = this.initRsStyle()
      }
    },
    setContourPoint (contourPoint, ref) {
      let i = this.basePoint.bpRef.indexOf(ref)
      if (i > -1) {
        this.basePoint.contourPointList.splice(i, 1, contourPoint)
      } else {
        this.basePoint.contourPointList.push(contourPoint)
      }
    },
    moverEnd (grap, index) { // 移动结束：存放基点信息，并且存放基点list
      this.svgContentList[grap.name][index] = grap
      let i = this.basePoint.bpRef.indexOf(grap.ref)
      if (i > -1) { // 新增/修改基点List
        this.basePoint.pointList.splice(i, 1, grap.lrList)
      } else {
        this.basePoint.pointList.push(grap.lrList) // 上右下左中
        this.basePoint.bpRef.push(grap.ref) // 上右下左中
        i = this.basePoint.pointList.length - 1
      }
      this.promptLine = []
      this.comparedBp(grap, i)
      setTimeout(() => {
        this.promptLine = []
      }, 1000)
      // this.makeContour(grap, i, true)
    },
    // 更新/新增轮廓矩形
    // TODO,优化： 诺只是拖动部分点，只需要计算改变了的那个点有没有影响矩形就行了
    makeContour (grap, index, show) {
      let obj = {}
      let x = NaN
      let y = NaN
      let xMax = NaN
      let yMax = NaN
      let width = NaN
      let height = NaN
      let conKey = [grap.childRef, 'con'].join('')
      for (let i = 0, l = grap.lrList.length; i < l; i++) { // 计算最小点和最大点
        let cx = Number(grap.lrList[i].style.cx)
        let cy = Number(grap.lrList[i].style.cy)
        if (i === 0) {
          x = cx
          y = cy
          xMax = cx
          yMax = cy
        }
        if (cx < x) {
          x = cx
        } else if (cx > xMax) {
          xMax = cx
        }
        if (cy < y) {
          y = cy
        } else if (cy > yMax) {
          yMax = cy
        }
      }
      width = xMax - x
      height = yMax - y
      obj = {
        conKey, x, y, width, height, show
      }
      obj = this.makeAnchorCir(obj)
      if (index === -1) {
        this.contourList.push(obj)
      } else {
        this.contourList.splice(index, 1, obj)
      }
      setTimeout(() => {
        obj.show = false
      }, 1000)
    },
    // TODO 优化： 修改的时候应该不改变 每一个 cirKey
    makeAnchorCir (obj) {
      let cirList = Array(8)
      cirList[0] = {
        x: obj.x - 8,
        y: obj.y - 8,
        cirKey: [obj.conKey, 0].join('')
      }
      cirList[1] = {
        x: obj.x + obj.width / 2 - 8,
        y: obj.y - 8,
        cirKey: [obj.conKey, 1].join('')
      }
      cirList[2] = {
        x: obj.x + obj.width - 8,
        y: obj.y - 8,
        cirKey: [obj.conKey, 2].join('')
      }
      cirList[3] = {
        x: obj.x - 8,
        y: obj.y + obj.height / 2 - 8,
        cirKey: [obj.conKey, 3].join('')
      }
      cirList[4] = {
        x: obj.x + obj.width - 8,
        y: obj.y + obj.height / 2 - 8,
        cirKey: [obj.conKey, 4].join('')
      }
      cirList[5] = {
        x: obj.x - 8,
        y: obj.y + obj.height - 8,
        cirKey: [obj.conKey, 5].join('')
      }
      cirList[6] = {
        x: obj.x + obj.width / 2 - 8,
        y: obj.y + obj.height - 8,
        cirKey: [obj.conKey, 6].join('')
      }
      cirList[7] = {
        x: obj.x + obj.width - 8,
        y: obj.y + obj.height - 8,
        cirKey: [obj.conKey, 7].join('')
      }
      obj.cirList = cirList
      return obj
    },
    movering (grap, index) { // 移动中
      let i = this.basePoint.bpRef.indexOf(grap.ref)
      this.promptLine = []
      this.comparedBp(grap, i)
      // this.makeContour(grap, i)
    },
    comparedBp (grap, i) { // 寻找提示线
      let that = this
      for (let j = 0, l = grap.lrList.length; j < l; j++) { // 对比
        for (let m = 0, l = that.basePoint.bpRef.length; m < l; m++) {
          if (m === i) continue
          let onePointList = that.basePoint.pointList[m]
          for (var n = 0; n < onePointList.length; n++) {
            if (grap.lrList[j].style.cx === onePointList[n].style.cx) {
              that.promptLine.push({
                x1: onePointList[n].style.cx,
                x2: onePointList[n].style.cx,
                y1: that.bgLineList.ys,
                y2: that.bgLineList.ye
              })
            } else if (grap.lrList[j].style.cy === onePointList[n].style.cy) {
              that.promptLine.push({
                x1: that.bgLineList.xs,
                x2: that.bgLineList.xe,
                y1: onePointList[n].style.cy,
                y2: onePointList[n].style.cy
              })
            }
          }
        }
      }
    },
    // 从工具栏开始拖拽
    dragstart (ev, obj) {
      ev.dataTransfer.setData('Text', ev.target.id)
      this.isDraging = JSON.parse(JSON.stringify(obj))
    },
    allowDrop (ev) {
      ev.preventDefault()
    },
    // 拖拽到位
    drop (ev) {
      this.offset.left = 103
      ev.preventDefault()
      this.client.x = ev.clientX
      this.client.y = ev.clientY
      this.isDraging.ref = this.isDraging.ref + this.svgContentList[this.isDraging.name].length
      this.isDraging.childRef = this.isDraging.childRef + this.svgContentList[this.isDraging.name].length
      this.svgContentList[this.isDraging.name].push(this.isDraging)
    },
    getSvg (e, leftOffset) {
      leftOffset = leftOffset || 0
      this.baseGrap.ec.xs = true
      this.baseGrap.ec.ys = true
      this.baseGrap.ec.cs = true
      let s = this.$refs.mySvg
      let svgStyle = document.defaultView.getComputedStyle(s)
      // 计算三个小圆点的位置
      const xleft = Number(svgStyle.cx.substring(0, svgStyle.cx.length - 2)) + Number(svgStyle.rx.substring(0, svgStyle.rx.length - 2)) - 3 + leftOffset
      const xtop = Number(svgStyle.cy.substring(0, svgStyle.cy.length - 2)) - 3
      const yleft = Number(svgStyle.cx.substring(0, svgStyle.cx.length - 2)) - 3 + leftOffset
      const ytop = Number(svgStyle.cy.substring(0, svgStyle.cy.length - 2)) + Number(svgStyle.ry.substring(0, svgStyle.ry.length - 2)) - 3
      const cleft = Number(svgStyle.cx.substring(0, svgStyle.cx.length - 2)) - 3 + leftOffset
      const ctop = Number(svgStyle.cy.substring(0, svgStyle.cy.length - 2)) - 3
      this.baseGrap.ec.lrList[0].style.left = this.addPx(xleft)
      this.baseGrap.ec.lrList[0].style.top = this.addPx(xtop)
      this.baseGrap.ec.lrList[1].style.left = this.addPx(yleft)
      this.baseGrap.ec.lrList[1].style.top = this.addPx(ytop)
      this.baseGrap.ec.lrList[2].style.left = this.addPx(cleft)
      this.baseGrap.ec.lrList[2].style.top = this.addPx(ctop)
    },
    // changeSvg (x, y, direction) {
    //   if (direction === 'r') {
    //     this.baseGrap.ec.ys = false
    //     this.baseGrap.ec.cs = false
    //     this.baseGrap.ec.svgConfig.rx = x - this.baseGrap.ec.svgConfig.cx + 3 - this.offset.left
    //   } else if (direction === 'b') {
    //     this.baseGrap.ec.xs = false
    //     this.baseGrap.ec.cs = false
    //     this.baseGrap.ec.svgConfig.ry = y - this.baseGrap.ec.svgConfig.cy + 3
    //   } else if (direction === 'c') {
    //     this.baseGrap.ec.ys = false
    //     this.baseGrap.ec.xs = false
    //     this.baseGrap.ec.svgConfig.cx = x - 3 - this.offset.left
    //     this.baseGrap.ec.svgConfig.cy = y - 3
    //   }
    // },
    // lrMd_x (e, type) {
    //   this.lrMd(e, type, this.baseGrap.ec.lrList[0])
    // },
    // lrMd (e, direction, lrSty) {
    //   let mt = e.currentTarget
    //   const disX = e.clientX - mt.offsetLeft
    //   const disY = e.clientY - mt.offsetTop
    //   document.onmousemove = (e) => {
    //     let left = e.clientX - disX
    //     let top = e.clientY - disY
    //     lrSty.top = this.addPx(top)
    //     lrSty.left = this.addPx(left)
    //     this.changeSvg(left, top, direction)
    //   }
    //   document.onmouseup = (e) => {
    //     document.onmousemove = null
    //     document.onmouseup = null
    //     mt = null
    //   }
    // },
    addPx (num) {
      return [num, 'px'].join('')
    }
  }
}
</script>

<style lang="postcss" scoped>
.canvas__round {
  position: absolute;
  display: inline-block;
  width: 6px;
  height: 6px;
  border: 1px solid #ed3f14;
  border-radius: 50%;
}
.my-svg {
  width: auto;
  height: auto;
  position: absolute;
  display: inline-block;
}
div.mxRubberband {
  position: absolute;
  background: #99ccff;
  overflow: hidden;
  border: 1px solid #00d;
  opacity: 0.3;
}
</style>



