import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import gomoku from '@/components/gomoku/gomoku'
import canvas from '@/components/canvas'
import svg from '@/components/chartFlow/svg'
import language from '@/components/language'
import cssTest from '@/components/cssTest'
// import three from '@/components/myThree.js'
// import three from '@/components/three'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      children: [
        {
          path: '/',
          name: 'gomoku',
          component: gomoku
        },
        {
          path: '/svg',
          name: 'svg',
          component: svg
        },
        {
          path: '/canvas',
          name: 'canvas',
          component: canvas
        },
        {
          path: '/language',
          name: 'language',
          component: language
        },
        // {
        //   path: '/three',
        //   name: 'three',
        //   component: three
        // },
        {
          path: '/cssTest',
          name: 'cssTest',
          component: cssTest
        }
      ]
    }
  ]
})



