<template>
<g>
  <path :d="grap.svgConfig.d" draggable="false" @mousedown.prevent='mdGrap' @click="singleSel(true)"
      style="stroke: #000;stroke-width: 4;cursor: all-scroll;" fill="none" :ref="grap.childRef"/>
  <circle
    v-for="(lr, index) in grap.lrList" v-if="grap.lrList.length > 0" :key="index"
    @mousedown.prevent="circleMd($event, lr, index)" @mouseover.prevent='expandArea(lr)'
    @mouseout.prevent='narrowArea(lr)'
    :cx="lr.style.cx" :cy="lr.style.cy" :r="lr.style.r" :stroke="lr.style.fill"
    :stroke-width="cirStyle.strokeWidth" :fill="lr.style.fill" />
  <contour ref='mycontour' :grap='grap' :mulSelected='mulSelected' @changeGrap='changeGrap'
    :singleSelected='singleSelected' :offset='offset'></contour>
</g>
</template>

<script>
import contour from '@/components/chartFlow/contour'
export default {
  components: {
    contour
  },
  props: {
    grap: {
      type: Object,
      require: true
    },
    offset: {
      type: Object
    },
    client: {
      type: Object
    },
    bgLineList: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data () {
    return {
      cirStyle: {
        r: 4,
        strokeWidth: 1,
        stroke: '#ed3f14'
      },
      gridLen: 8,
      moveCir: NaN,
      mulSelected: false, // 多个被选中图形中的一个
      singleSelected: false, // 被单独选中
      oldGrap: {},
      cirMoveing: false // 是否正在移动锚点
    }
  },
  mounted () {
    this.$nextTick(() => {
      let x = this.client.x - this.offset.left
      let y = this.client.y - this.offset.top
      this.changeSvg(x, y, 'move', this.gridLen)
      this.$nextTick(() => {
        this.makeLr()
        this.moverEnd()
      })
    })
  },
  methods: {
    setGrapInfo () {
      return this.grap
    },
    changeGrap (x) {
      console(x)
    },
    singleSel (show) {
      this.singleSelected = !!show
    },
    showThisContour (show) {
      this.mulSelected = !!show
    },
    moverEnd () { // 移动完成
      this.correctSvg(this.gridLen)
      this.$nextTick(() => { // 纠正锚点位置
        this.makeLr()
        this.$emit('moverEnd', this.grap, this.index)
        let contourPoint = this.$refs.mycontour.createdContour(true) // 更新轮廓
        this.$emit('setContourPoint', contourPoint, this.grap.ref)
        this.oldGrap = JSON.parse(JSON.stringify(this.grap))
      })
    },
    isMoverign () { // 正在移动
      this.$nextTick(() => { // 纠正锚点位置
        this.makeLr()
        this.$emit('movering', this.grap, this.index)
        this.$refs.mycontour.createdContour(false) // 更新轮廓
      })
    },
    // TODO 拖动时不需要每次都跟新所有点的坐标，只需要跟新我拖动影响到的那几个点的坐标
    makeLr () { // 纠正锚点位置
      // let s = this.$refs[this.grap.childRef]
      // let svgStyle = document.defaultView.getComputedStyle(s)
      // console.log(svgStyle)
      // 通过d获取三个锚点位置
      const dArr = this.grap.svgConfig.d.split(' ')
      const lPos = []
      // 计算折点和中点位置
      for (let i = 0, l = dArr.length; i < l; i++) {
        if (dArr[i] === 'L') {
          lPos.push(i)
        }
      }
      this.grap.lrList = []
      for (var i = 0; i < lPos.length * 2 + 1; i++) {
        let cx = 0
        let cy = 0
        let type = ''
        let centeri
        let r = 4
        let fill = 'rgb(78, 244, 113)'
        if (i % 2 !== 0) { // 是中间点
          r = 3
          fill = 'rgba(122, 223, 143, 0.5)'
          centeri = Math.ceil(i / 2) * 3 // 对应L的位置
          cx = (Number(dArr[centeri - 2]) + Number(dArr[centeri + 1])) / 2
          cy = (Number(dArr[centeri - 1]) + Number(dArr[centeri + 2])) / 2
          type = 'c'
        } else {
          centeri = Math.ceil((i + 1) / 2) * 3 // 对应L的位置
          cx = Number(dArr[centeri - 2])
          cy = Number(dArr[centeri - 1])
          type = 'f' // fold 缩写
        }
        let obj = {
          type,
          style: {
            left: '',
            top: '',
            fill,
            r,
            cx,
            cy
          }
        }
        this.grap.lrList.push(obj)
      }
      // this.grap.lrList[0].style.cx = dArr[1]
      // this.grap.lrList[0].style.cy = dArr[2] // 起
      // this.grap.lrList[1].style.cx = dArr[dArr.length - 2]
      // this.grap.lrList[1].style.cy = dArr[dArr.length - 1] // 终
      // this.grap.lrList[2].style.cx = Math.round((Number(dArr[1]) + Number(dArr[4])) / 2)
      // this.grap.lrList[2].style.cy = Math.round((Number(dArr[2]) + Number(dArr[5])) / 2)
    },
    narrowArea (lr) { // 鼠标移开锚点
      let lrSty = lr.style
      if (lr.type === 'f') {
        lrSty.r = 4
        return
      }
      lrSty.r = 3
      lrSty.fill = 'rgba(122, 223, 143, 0.5)'
    },
    expandArea (lr) { // 鼠标放在锚点上
      let lrSty = lr.style
      lrSty.r = 5
      lrSty.fill = 'rgb(78, 244, 113)'
    },
    mdGrap (e, isfollow) {
      const x = e.clientX
      const y = e.clientY
      let disX = NaN
      let disY = NaN
      document.onmousemove = (e) => {
        disX = e.clientX - x
        disY = e.clientY - y
        this.moveSvg(disX, disY)
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
        this.moverEnd()
        if (this.mulSelected) {
          this.$emit('moveAllSelGrapEnd', this.gridLen, this.grap.ref)
        }
      }
    },
    circleMd (e, lr, index) { // 拖动锚点
      let direction = lr.type
      let lrSty = lr.style
      this.moveCir = index
      // let mt = e.currentTarget
      this.cirMoveing = true
      // let cirStyle = document.defaultView.getComputedStyle(mt)
      const disX = e.clientX - lrSty.cx - this.offset.left
      const disY = e.clientY - lrSty.cy - this.offset.top
      document.onmousemove = (e) => {
        let cx = e.clientX - disX - this.offset.left
        let cy = e.clientY - disY - this.offset.top
        lrSty.cx = cx
        lrSty.cy = cy
        console.log(cx, cy)
        this.cgSvg(cx, cy, direction)
        if (direction === 'c') {
          this.moveCir++
          direction = 'f'
        }
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
        // let cx = e.clientX - disX - this.offset.left
        // let cy = e.clientY - disY - this.offset.top
        this.correctSvg(this.gridLen)
        this.cirMoveing = false
        this.moverEnd() // 移动结束
        // mt = null
      }
    },
    correctSvg (len) {
      let dArr = this.grap.svgConfig.d.split(' ')
      for (var i = 0; i < dArr.length; i++) {
        if (i % 3 === 1 || i % 3 === 2) {
          let newD = Number(dArr[i])
          dArr[i] = Math.round(newD / len) * len
        }
      }
      this.grap.svgConfig.d = dArr.join(' ')
      this.isMoverign()
    },
    moveSvg (disX, disY, isfollow) {
      let dArr = this.oldGrap.svgConfig.d.split(' ')
      for (var i = 0; i < dArr.length; i++) {
        if (i % 3 === 1) {
          dArr[i] = Number(dArr[i]) + disX
        } else if (i % 3 === 2) {
          dArr[i] = Number(dArr[i]) + disY
        }
      }
      this.grap.svgConfig.d = dArr.join(' ')
      let littleMoveLen = this.gridLen / 4
      this.correctSvg(littleMoveLen)
      this.isMoverign()
      if (this.mulSelected && !isfollow) {
        this.$emit('moveAllSelGrap', disX, disY, this.grap.ref)
      }
    },
    cgSvg (x, y, direction) { // 正在移动
      let littleMoveLen = this.gridLen / 4
      this.changeSvg(x, y, direction, littleMoveLen)
      this.isMoverign()
    },
    changeSvg (x, y, direction, len) { // 跟新图形
      console.log('direction', direction)
      let dArr = this.grap.svgConfig.d.split(' ')
      // TODO 删除
      let x1 = Number(dArr[1])
      let y1 = Number(dArr[2])
      let x2 = Number(dArr[dArr.length - 2])
      let y2 = Number(dArr[dArr.length - 1])
      // 需要知道改变的是第几个点
      if (direction === 'f') {
        let di = this.cirIndexToDarr(this.moveCir, 'f') // 找出要改变的两个点
        x1 = Math.round(x / len) * len
        y1 = Math.round(y / len) * len
        dArr[di[0]] = x1
        dArr[di[1]] = y1
        this.grap.svgConfig.d = dArr.join(' ')
      } else if (direction === 'move') {
        // TODO 点着线移动
        let cx = Math.round(x / len) * len
        let cy = Math.round(y / len) * len // 中点xy
        x1 = cx - (Number(dArr[4]) - Number(dArr[1])) / 2
        x2 = cx + (Number(dArr[4]) - Number(dArr[1])) / 2
        y1 = cy - (Number(dArr[5]) - Number(dArr[2])) / 2
        y2 = cy + (Number(dArr[5]) - Number(dArr[2])) / 2
        this.grap.svgConfig.d = ['M', x1, y1, 'L', x2, y2].join(' ')
      }
      if (direction === 'c') {
        let di = this.cirIndexToDarr(this.moveCir, 'c') // di处加个L。 后面加上cx， cy
        let dx = Math.round(x / len) * len
        let dy = Math.round(y / len) * len // 中点xy
        dArr.splice(di, 0, 'L', dx, dy)
        this.grap.svgConfig.d = dArr.join(' ')
      }
    },
    cirIndexToDarr (i, type) { // 第i个小圆对应在d中第几个值（折点）
      i = Number(i)
      if (type === 'f') { // 折点断电（端点）
        let ei = Math.ceil((i + 1) / 2) * 3
        return [ei - 2, ei - 1]
      } else if (type === 'c') { // 线段中间点
        return Math.ceil((i + 1) / 2) * 3
      }
    },
    addPx (num) {
      return [num, 'px'].join('')
    }
  }
}
</script>

<style>
</style>



