<template>
<g>
  <!--<ellipse v-if="grap.svgConfig.ecs.length > 0" v-for="(ec, index) in grap.svgConfig.ecs" :key="'myGrapEc' + index"
    :cx="ec.svgConfig.cx" :cy="ec.svgConfig.cy" :rx="ec.svgConfig.rx" :ry="ec.svgConfig.ry"
    class="my-svg" fill="none" style="stroke-width:1; stroke:rgb(0,0,0);" />
  <path v-if="grap.svgConfig.pls.length > 0" v-for="(pl, index) in grap.svgConfig.pls" :d="pl.svgConfig.d"
    :key="'myGrapPl' + index"  fill="none" style="stroke: #333;stroke-width: 4" />-->
  <my-ellipse v-if="grap.svgConfig.ecs.length > 0"
    v-for="(ec, index) in grap.svgConfig.ecs" :key="'myGrapEc' + index"
    :bgLineList='bgLineList' :client='client' :offset='offset' @moveAllSelGrap='moveAllSelGrap'
    @moveAllSelGrapEnd='moveAllSelGrapEnd'
    @movering='movering' @moverEnd='moverEnd' @setContourPoint='setContourPoint'
    :index='index' :ref='ec.ref' :grap="ec">
  </my-ellipse>
  <my-pl v-if="grap.svgConfig.pls.length > 0"
    v-for="(pl, index) in grap.svgConfig.pls" :key="'myGrapPl' + index"
    :bgLineList='bgLineList' :client='client' :offset='offset' @moveAllSelGrap='moveAllSelGrap'
    @moveAllSelGrapEnd='moveAllSelGrapEnd'
    @movering='movering' @moverEnd='moverEnd' @setContourPoint='setContourPoint'
    :index='index' :ref='pl.ref' :grap="pl">
  </my-pl>
</g>
</template>

<script>
import myEllipse from '@/components/chartFlow/my-ellipse'
import myPl from '@/components/chartFlow/my-pl'
export default {
  components: {
    myEllipse,
    myPl
  },
  props: {
    grap: {
      type: Object,
      require: true
    },
    offset: {
      type: Object
    },
    index: {
      type: Number
    },
    bgLineList: {
      type: Object
    },
    client: {
      type: Object
    }
  },
  data () {
    return {
      leftOffset: 20,
      topOffset: 40
    }
  },
  computed: {
    // 计算属性的 getter
    myIndex () {
      // `this` 指向 vm 实例
      return this.index + 1
    }
  },
  mounted () {
    this.$nextTick(() => {
      // 图形初始化
      if (this.grap.svgConfig.pls.length > 0) {
        for (let i = 0, l = this.grap.svgConfig.pls.length; i < l; i++) {
          let item = this.grap.svgConfig.pls[i]
          let dArr = item.svgConfig.d.split(' ')
          for (var j = 0; j < dArr.length; j++) {
            if (j % 3 === 1) {
              dArr[j] = (Number(dArr[j]) + this.bgLineList.xs + this.leftOffset * this.myIndex)
            } else if (j % 3 === 2) {
              dArr[j] = (Number(dArr[j]) + this.topOffset * this.myIndex)
            }
          }
          item.svgConfig.d = dArr.join(' ')
        }
      }
      if (this.grap.svgConfig.ecs.length > 0) {
        for (let i = 0, m = this.grap.svgConfig.ecs.length; i < m; i++) {
          let item = this.grap.svgConfig.ecs[i]
          item.svgConfig.cx = item.svgConfig.cx + this.bgLineList.xs + this.leftOffset * this.myIndex
          item.svgConfig.cy = item.svgConfig.cy + this.topOffset * this.myIndex
        }
      }
    })
  },
  methods: {
    getGrap () {
      this.$emit('on-click', JSON.stringify(this.myGraps))
    }
  }
}
</script>

<style>
</style>



