      <label for="">email：</label>
      <input type="text" v-model="formDetailInfo.email" />
    </div>
    <div>
      <label for="">生日：</label>
      <input type="text" v-model="formDetailInfo.birthday" />
      <label for="">性别：</label>
      <input type="text" v-model="formDetailInfo.sex" />
      <label for="">省：</label>
      <input type="text" v-model="formDetailInfo.province" />
      <label for="">市：</label>
      <input type="text" v-model="formDetailInfo.city" />
      <label for="">县：</label>
      <input type="text" v-model="formDetailInfo.xian" />
      <label for="">地址：</label>
      <input type="text" v-model="formDetailInfo.address" />
    </div>
    <div style="display: inline-flex;justify-content: space-between; margin-top: 20px; align-items:flex-end">
      <textarea v-model="userInfo" rows="4" cols="50" style="margin-right: 10px;"></textarea>
      <div class="svg-btn__md" @click="intelligentRecognition">智能识别</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userInfo: '',
      textCache: '',
      formDetailInfo: {
        realName: '',
        email: '',
        birthday: '',
        sex: '',
        province: '',
        city: '',
        xian: '',
        address: ''
      }
    }
  },
  created () {
    this.test()
  },
  methods: {
    /* eslint-disable */
    test () {
      var a = true
      if (a) {
        var color = 'red' // 在块级作用域外也可以使用, let有块级作用域， var没有
      }
      console.log('color', color) // 'red'
    },
    intelligentRecognition () {
      const regions = JSON.parse(JSON.stringify(this.regions))
      this.formDetailInfo.realName = ''
      this.formDetailInfo.address = ''
      let that = this
      const emailPattern = /([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})/
      const phonePattern = /(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}/
      // (-|/|.| ) 中间可以是这些符号
      const birPattern = /(19|20)\d{2}[-|/|.| ](1[0-2]|0?[1-9])[-|/|.| ](0?[1-9]|[1-2][0-9]|3[0-1])/
      const birPattern2 = /(19|20)\d{2}[-|/|.| ](1[0-2]|0?[1-9])[-|/|.| ](0[1-9]|[1-2][0-9]|3[0-1])/
      const sexPattern = /^男|女$/
      this.textCache = this.userInfo
      if (this.textCache.match(phonePattern)) {
        this.phone = this.textCache.match(phonePattern)[0]
        this.textCache = this.textCache.replace(phonePattern, '')
      }
      let birMatch2 = this.textCache.match(birPattern2)
      if (birMatch2) {
        this.formDetailInfo.birthday = birMatch2[0]
        this.textCache = this.textCache.replace(birPattern2, '')
      } else {
        let birMatch = this.textCache.match(birPattern)
        if (birMatch) {
          this.formDetailInfo.birthday = birMatch[0]
          this.textCache = this.textCache.replace(birPattern, '')
        }
      }
      if (this.textCache.match(emailPattern)) {
        this.formDetailInfo.email = this.textCache.match(emailPattern)[0]
        this.textCache = this.textCache.replace(emailPattern, '')
      }
      this.textCache = this.textCache.replace(/ |[^\w\s^\u4e00-\u9fa5]/g, ',')
      let textArr = this.textCache.split(',')
      let j = NaN
      for (let i = 0; i < textArr.length; i++) {
        if (textArr[i] === '') {
          textArr.splice(i, 1)
          i = i - 1
          continue
        }
        if (textArr[i].match(sexPattern)) {
          // this.formDetailInfo.sex = textArr[i] === '男' ? 0 : 1
          this.formDetailInfo.sex = textArr[i]
          textArr.splice(i, 1)
          j = i
          i = i - 1
          continue
        }
      }
      this.textCache = textArr.join(',')
      // 识别区域
      this.formDetailInfo['region-code'] = ''
      let regionCode = ''
      let regionIndex = '' // 记录区域文本开始的位置
      let regionText = '' // 记录区域文本
      regions.forEach(province => { // 默认必须是 省--市--县，或者 市--县，不能 省--县
        // if (regionCode) {
        //   break
        // }
        let pIndex = that.textCache.indexOf(province.label)
        if (pIndex === -1) {
          province.label = province.label.replace('省', '')
          pIndex = that.textCache.indexOf(province.label)
        }
        if (pIndex > -1) { // 找到一级区域
          that.formDetailInfo.province = province.label
          that.formDetailInfo.realName = that.textCache.substr(0, pIndex)
          that.textCache = that.textCache.replace(province.label, '')
          if (!province.isLeaf) {
            let t = that.textCache.substr(pIndex)
            t = t.replace(province.label, '') // 根据有限状态机， 市必须在省后面， t是去掉省和省前面的字的剩余文本
            province.children.forEach(city => {
              if (t.indexOf(city.label) === -1) {
                city.label = city.label.replace('市', '')
                pIndex = t.indexOf(city.label)
              }
              if (t.indexOf(city.label) === 0) {
                that.formDetailInfo.city = city.label
                that.textCache = that.textCache.replace(city.label, '')
                t = t.replace(city.label, '') // t文本中去掉市
                if (!city.isLeaf) {
                  city.children.forEach(val => {
                    if (t.indexOf(val.label) === 0) {
                      that.textCache = that.textCache.replace(val.label, '')
                      regionCode = val.value
                      that.formDetailInfo.xian = val.label
                      // break
                    }
                  })
                }
              }
            })
          }
        }
      })
      // 如果在上面的循环中没有找到相应区域，可能是因为没有写 省份
      if (!regionCode) {
        regions.forEach(province => {
          // if (regionCode) {
          //   break
          // }
          if (!province.isLeaf) { // 直接寻找对应的市
            province.children.forEach(city => {
              let cIndex = that.textCache.indexOf(city.label)
              if (cIndex === -1) {
                city.label = city.label.replace('市', '')
                cIndex = that.textCache.indexOf(city.label)
              }
              if (cIndex > -1) { // 找到了市
                this.formDetailInfo.realName = that.textCache.substr(0, cIndex)
                that.textCache = that.textCache.replace(city.label, '')
                that.formDetailInfo.city  = city.label
                if (!city.isLeaf) {
                  let t = this.textCache.substr(cIndex)
                  t = t.replace(city.label, '') // 根据有限状态机， 县跟在市后面
                  city.children.forEach(val => {
                    if (t.indexOf(val.label) === 0) {
                      that.textCache = that.textCache.replace(val.label, '')
                      that.formDetailInfo.xian  = val.label
                      regionCode = val.value
                      // break
                    }
                  })
                }
              }
            })
          }
        })
      }
      this.formDetailInfo['region-code'] = regionCode
      
      // this.formDetailInfo.address = this.textCache.replace(/,/g, '')
      if (this.formDetailInfo.realName) {
        this.textCache = this.textCache.replace(this.formDetailInfo.realName, '')
        this.formDetailInfo.realName = this.formDetailInfo.realName.replace(/,/g, '')
        this.formDetailInfo.address = this.textCache.replace(/,/g, '')
      } else {
        textArr = this.textCache.split(',')
        for (let i = 0; i < textArr.length; i++) {
          if (textArr[i] === '') {
            textArr.splice(i, 1)
            i = i - 1
            continue
          }
        }
        this.formDetailInfo.realName = textArr[textArr.length - 1]
        let address = this.textCache.replace(this.formDetailInfo.realName, '')
        this.formDetailInfo.address = address.replace(/,/g, '')
      }
      // for (let i = 0; i < j; i++) {
      //   this.formDetailInfo.realName += textArr[i]
      // }
      // for (let i = j; i < textArr.length; i++) {
      //   this.formDetailInfo.address += textArr[i]
      // }
      /* eslint-disable */
    }
