<template>
<g>
  <ellipse
    :cx="grap.svgConfig.cx" :cy="grap.svgConfig.cy" :rx="grap.svgConfig.rx" :ry="grap.svgConfig.ry"
    @mousedown.prevent='mdGrap' class="my-svg" @click='singleSel(true)'
    style="fill: rgba(255, 255, 255, 0);stroke-width:1; stroke:rgb(0,0,0);cursor: all-scroll;"
    :ref="grap.childRef" id='elsvg' draggable="false" pointer-events="all"/>
  <circle
    v-for="(lr, index) in grap.lrList" v-if="grap.lrList.length > 0"
    :key="index" @mousedown.prevent="circleMd($event, lr.type, lr.style, index)"
    :cx="lr.style.cx" :cy="lr.style.cy" :r="cirStyle.r" :stroke="cirStyle.stroke"
    :stroke-width="cirStyle.strokeWidth" fill="red" />
  <contour ref='mycontour' :grap='grap' :mulSelected='mulSelected' @changeGrap='changeGrap'
    :singleSelected='singleSelected' :offset='offset'></contour>
</g>
</template>

<script>
import contour from '@/components/chartFlow/contour'
import util from '@/components/lib/util'
export default {
  components: {
    contour
  },
  props: {
    grap: {
      type: Object,
      require: true
    },
    offset: {
      type: Object
    },
    client: {
      type: Object
    },
    bgLineList: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data () {
    return {
      cirStyle: {
        r: 3,
        strokeWidth: 1,
        stroke: '#ed3f14'
      },
      oldGrap: {},
      gridLen: 8,
      moveCir: NaN,
      mulSelected: false, // 多个被选中图形中的一个
      singleSelected: false, // 被单独选中
      cirMoveing: false // 是否正在移动锚点
    }
  },
  mounted () {
    this.$nextTick(() => {
      let x = this.client.x - this.offset.left
      let y = this.client.y - this.offset.top
      this.changeSvg(x, y, 'c', this.gridLen)
      this.$nextTick(() => {
        this.makeLr()
        this.moverEnd()
      })
    })
  },
  methods: {
    // changeGrap (cx, cy, pos, cirList) {
    //   console.log(cx, cy, pos)
    //   if (pos === 't') {
    //     console.log('移动上边')
    //   }
    //   if (pos === 'lt') {
    //     let cx2 = cirList[7].x
    //     let cy2 = cirList[7].y
    //     this.getGrapByTwoPoint(cx, cy, cx2, cy2)
    //   } else if (pos === 'rt') {
    //     let cx2 = cirList[5].x
    //     let cy2 = cirList[5].y
    //     this.getGrapByTwoPoint(cx, cy, cx2, cy2)
    //   } else if (pos === 'rb') {
    //     let cx2 = cirList[0].x
    //     let cy2 = cirList[0].y
    //     this.getGrapByTwoPoint(cx, cy, cx2, cy2)
    //   } else if (pos === 'lb') {
    //     let cx2 = cirList[2].x
    //     let cy2 = cirList[2].y
    //     this.getGrapByTwoPoint(cx, cy, cx2, cy2)
    //   }
    //   this.isMoverign()
    // },
    setGrapInfo () {
      return this.grap
    },
    changeGrap (cx, cy, cx2, cy2, pos) {
      let point = this.getTwoPoint(cx, cy, cx2, cy2)
      if (pos === 't' || pos === 'b') {
        this.getGrapByTBPoint(point.p1, point.p2)
      } else if (pos === 'lm' || pos === 'rm') {
        this.getGrapByLRPoint(point.p1, point.p2)
      } else {
        this.getGrapByTwoAngPoint(point.p1, point.p2)
      }
      this.isMoverign()
    },
    getGrapByTBPoint (p1, p2) { // 由上下两点确定图形
      let cen = util.getCenterByTwoPoint(p1, p2)
      this.grap.svgConfig.cy = cen.y
      let ry = Math.abs(p1.y - p2.y) / 2
      this.grap.svgConfig.ry = ry
    },
    getGrapByLRPoint (p1, p2) { // 由左右两点确定图形
      let cen = util.getCenterByTwoPoint(p1, p2)
      this.grap.svgConfig.cx = cen.x
      let rx = Math.abs(p1.x - p2.x) / 2
      this.grap.svgConfig.rx = rx
    },
    getGrapByTwoAngPoint (p1, p2) { // 由对角线确定图形
      let cen = util.getCenterByTwoPoint(p1, p2)
      let rx = Math.abs(p1.x - p2.x) / 2
      let ry = Math.abs(p1.y - p2.y) / 2
      this.grap.svgConfig.cx = cen.x
      this.grap.svgConfig.cy = cen.y
      this.grap.svgConfig.rx = rx
      this.grap.svgConfig.ry = ry
    },
    getTwoPoint (cx, cy, cx2, cy2) {
      const l = this.gridLen / 4
      let p1 = {x: this.orderNum(cx, l), y: this.orderNum(cy, l)}
      let p2 = {x: cx2, y: cy2}
      return {p1, p2}
    },
    orderNum (num, l) {
      return Math.round(num / l) * l
    },
    singleSel (show) {
      this.singleSelected = !!show
    },
    showThisContour (show) {
      this.mulSelected = !!show
    },
    // 移动结束时将 grap 和 contourPoint传出去。形成grap的ref的list和contourPoint的list和grap所有锚点的list
    // 对比拖动范围确定哪些图形的contourPoint在拖动范围内
    moverEnd () { // 移动完成
      this.correctSvg(this.gridLen)
      this.$nextTick(() => { // 纠正锚点位置
        this.makeLr()
        this.$emit('moverEnd', this.grap, this.index)
        let contourPoint = this.$refs.mycontour.createdContour(true) // 更新轮廓
        this.$emit('setContourPoint', contourPoint, this.grap.ref)
        this.oldGrap = JSON.parse(JSON.stringify(this.grap))
      })
    },
    isMoverign () { // 正在移动
      this.$nextTick(() => { // 纠正锚点位置
        this.makeLr()
        this.$emit('movering', this.grap, this.index)
        this.$refs.mycontour.createdContour(false) // 更新轮廓
      })
    },
    makeLr () { // 纠正锚点位置
      // let s = this.$refs[this.grap.childRef]
      // let svgStyle = document.defaultView.getComputedStyle(s)
      // console.log(svgStyle)
      // 计算三个小圆点的位置
      const cx = this.grap.svgConfig.cx
      const cy = this.grap.svgConfig.cy
      const rx = this.grap.svgConfig.rx
      const ry = this.grap.svgConfig.ry
      this.grap.lrList[0].style.cx = cx
      this.grap.lrList[0].style.cy = cy - ry // 上
      this.grap.lrList[1].style.cx = cx + rx
      this.grap.lrList[1].style.cy = cy // 右
      this.grap.lrList[2].style.cx = cx
      this.grap.lrList[2].style.cy = cy + ry // 下
      this.grap.lrList[3].style.cx = cx - rx
      this.grap.lrList[3].style.cy = cy // 左
      this.grap.lrList[4].style.cx = cx
      this.grap.lrList[4].style.cy = cy // 中
    },
    circleMd (e, direction, lrSty, index) { // 拖动锚点
      this.moveCir = index
      // let mt = e.currentTarget
      this.cirMoveing = true
      // let cirStyle = document.defaultView.getComputedStyle(mt)
      const disX = e.clientX - lrSty.cx - this.offset.left
      const disY = e.clientY - lrSty.cy - this.offset.top
      document.onmousemove = (e) => {
        let cx = e.clientX - disX - this.offset.left
        let cy = e.clientY - disY - this.offset.top
        lrSty.cx = cx
        lrSty.cy = cy
        this.cgSvg(cx, cy, direction)
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
        let cx = e.clientX - disX - this.offset.left
        let cy = e.clientY - disY - this.offset.top
        this.changeSvg(cx, cy, direction, this.gridLen)
        this.cirMoveing = false
        this.moverEnd() // 移动结束
        // mt = null
      }
    },
    mdGrap (e) { // 平移
      const x = e.clientX
      const y = e.clientY
      let disX = NaN
      let disY = NaN
      document.onmousemove = (e) => {
        disX = e.clientX - x
        disY = e.clientY - y
        this.moveSvg(disX, disY)
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
        this.moverEnd()
        if (this.mulSelected) {
          this.$emit('moveAllSelGrapEnd', this.gridLen, this.grap.ref)
        }
      }
    },
    moveSvg (disX, disY, isfollow) {
      this.grap.svgConfig.cx = this.oldGrap.svgConfig.cx + disX
      this.grap.svgConfig.cy = this.oldGrap.svgConfig.cy + disY
      let littleMoveLen = this.gridLen / 4
      this.correctSvg(littleMoveLen)
      this.isMoverign()
      if (this.mulSelected && !isfollow) {
        this.$emit('moveAllSelGrap', disX, disY, this.grap.ref)
      }
    },
    correctSvg (len) {
      this.grap.svgConfig.cx = Math.round(this.grap.svgConfig.cx / len) * len
      this.grap.svgConfig.cy = Math.round(this.grap.svgConfig.cy / len) * len
      this.grap.svgConfig.rx = Math.round(this.grap.svgConfig.rx / len) * len
      this.grap.svgConfig.ry = Math.round(this.grap.svgConfig.ry / len) * len
    },
    cgSvg (x, y, direction) { // 正在移动
      let littleMoveLen = this.gridLen / 4
      this.changeSvg(x, y, direction, littleMoveLen)
      this.isMoverign()
    },
    changeSvg (x, y, direction, len) { // 跟新图形
      if (direction === 'r') {
        let rx = Math.round((x - this.grap.svgConfig.cx) / len) * len
        this.grap.svgConfig.rx = rx
      } else if (direction === 'l') {
        let rx = Math.round((this.grap.svgConfig.cx - x) / len) * len
        this.grap.svgConfig.rx = rx
      } else if (direction === 't') {
        let ry = Math.round((this.grap.svgConfig.cy - y) / len) * len
        this.grap.svgConfig.ry = ry
      } else if (direction === 'b') {
        let ry = Math.round((y - this.grap.svgConfig.cy) / len) * len
        this.grap.svgConfig.ry = ry
      } else if (direction === 'c') {
        let myX = Math.round(x / len) * len
        let myY = Math.round(y / len) * len
        myX = myX > this.bgLineList.xs ? myX < this.bgLineList.xe ? myX : this.bgLineList.xe : this.bgLineList.xs
        myY = myY > this.bgLineList.ys ? myY < this.bgLineList.ye ? myY : this.bgLineList.ye : this.bgLineList.ys
        this.grap.svgConfig.cx = myX
        this.grap.svgConfig.cy = myY
      }
    },
    addPx (num) {
      return [num, 'px'].join('')
    }
  }
}
</script>

<style>
</style>



