<template>
<div class="custom-tree-container">
  <!--<canvas id="myCanvas" width="200" height="200" style="border:1px solid #c3c3c3;">
    Your browser does not support the canvas element.
  </canvas>
  <canvas id="myCanvas2" width="200" height="200" style="border:1px solid #c3c3c3;">
    Your browser does not support the canvas element.
  </canvas>-->
  {{ a }}
  <div @mousedown.prevent="draw" id="testcanvas">
    <canvas id="myCanvas" class="my-canvas" width="200" height="100" style="border:1px solid #c3c3c3;">
      Your browser does not support the canvas element.
    </canvas>
  </div>
  <div v-for="(cr, index) in canvasRound" :key="index" :ref="cr.ref" :style="cr.sty" class="canvas__round"></div>
  <div @mousedown.prevent="draw" id="testcanvas">
    <canvas id="myCanvas2" class="my-canvas" width="400" height="400" style="border:1px solid #c3c3c3;">
      Your browser does not support the canvas element.
    </canvas>
  </div>
</div>
</template>

<script>
export default {
  mounted () {
    var c = document.getElementById('myCanvas')
    var cxt = c.getContext('2d')
    cxt.moveTo(10, 10)
    cxt.lineTo(150, 50)
    cxt.lineTo(10, 50)
    cxt.stroke()
  },
  data () {
    return {
      a: 1,
      canvasRound: [{
        ref: 'crMyCanvas',
        sty: {
          left: '',
          top: ''
        }
      }]
    }
  },
  methods: {
    getStyles (obj) { // 兼容FF，IE10; IE9及以下未测试
      return document.defaultView.getComputedStyle(obj)
    },
    getCanvasStyle (canvas) {
      return {
        rect: canvas.getBoundingClientRect(),
        leftB: parseInt(this.getStyles(canvas).borderLeftWidth),
        topB: parseInt(this.getStyles(canvas).borderTopWidth)
      }
    },
    // 已知鼠标坐标， 获取画布上的坐标
    getCanvasPos (canvas, e) { // 获取鼠标在canvas上的坐标(x, y)
      return {
        x: (e.clientX - this.getCanvasStyle(canvas).rect.left) - this.getCanvasStyle(canvas).leftB,
        y: (e.clientY - this.getCanvasStyle(canvas).rect.top) - this.getCanvasStyle(canvas).topB
      }
    },
    // 已知在画布上的位置（x, y） 获取绝对坐标
    getAbPos (canvas, pos) {
      let x = pos.x + this.getCanvasStyle(canvas).rect.left + this.getCanvasStyle(canvas).leftB
      let y = pos.y + this.getCanvasStyle(canvas).rect.top + this.getCanvasStyle(canvas).topB
      return {
        x, y
      }
    },
    draw (e) {
      document.onmousemove = (e) => {
        var c = document.getElementById('myCanvas2')
        var cxt = c.getContext('2d')
        // cxt.clearRect(0, 0, c.width, c.height)
        cxt.fillStyle = '#FF0000'
        cxt.beginPath()
        // cxt.arc(mousePos(e).x,mousePos(e).y,15,0,Math.PI*2,true)
        let pos = this.getCanvasPos(c, e)
        cxt.arc(pos.x, pos.y, 4, 0, Math.PI * 2, true)
        cxt.closePath()
        cxt.fill()
        const roundPos = this.getAbPos(c, pos)
        let rp = {
          x: roundPos.x - 3,
          y: roundPos.y - 3
        }
        this.canvasRound[0].sty.left = rp.x + 'px'
        this.canvasRound[0].sty.top = rp.y + 'px'
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
      }
    }
  }
}
</script>

<style lang="postcss" scoped>
  .canvas__round {
    position: fixed;
    display: inline-block;
    width: 6px;
    height: 6px;
    border: 1px solid #10e;
    border-radius: 50%;
  }
</style>



