<template>
<!-- eslint-disable -->
<g @mouseout.prevent="outReact($event)" v-if="con.show || mulSelected || singleSelected">
  <rect :x="con.x" :y="con.y" :width="con.width" :height="con.height" fill="none"
    stroke="#00a8ff" stroke-dasharray="3 3" pointer-events="none"></rect>
  <g v-for="cir in con.cirList" :key="cir.cirKey">
    <image :class="{'con-cir__ang1': cir.pos === 'lt' || cir.pos === 'rb', 'con-cir__side1': cir.pos === 't' || cir.pos === 'b', 'con-cir__side2': cir.pos === 'lm' || cir.pos === 'rm', 'con-cir__ang2': cir.pos === 'rt' || cir.pos === 'lb'}"
      @mousedown.prevent="circleMd($event, cir)" @mouseover.prevent="changeMousePos($event, cir)"
      :x="cir.x - 8" :y="cir.y - 8" width="16" height="16" xlink:href="data:image/png;base64," preserveAspectRatio="none"></image>
  </g>
</g>
<!-- eslint-enable -->
</template>

<script>
export default {
  props: {
    grap: {
      type: Object,
      require: true
    },
    mulSelected: {
      type: Boolean
    },
    singleSelected: {
      type: Boolean
    },
    offset: {
      type: Object
    }
  },
  data () {
    return {
      a: 0,
      con: {},
      mousePos: ''
    }
  },
  methods: {
    outReact (e) {
      // this.mousePos = ''
      // console.log(e)
      this.a = 1
    },
    changeMousePos (e, cir) {
      // this.mousePos = this.mousePos ? this.mousePos : cir.pos
      // console.log(e, cir)
      this.a = 1
    },
    circleMd (e, cir) {
      this.mousePos = cir.pos
      const disX = e.clientX - cir.x - this.offset.left
      const disY = e.clientY - cir.y - this.offset.top
      let cx2 = NaN
      let cy2 = NaN
      if (this.mousePos === 'lt') {
        cx2 = this.con.cirList[7].x
        cy2 = this.con.cirList[7].y
      } else if (this.mousePos === 'rt') {
        cx2 = this.con.cirList[5].x
        cy2 = this.con.cirList[5].y
      } else if (this.mousePos === 'lb') {
        cx2 = this.con.cirList[2].x
        cy2 = this.con.cirList[2].y
      } else if (this.mousePos === 'rb') {
        cx2 = this.con.cirList[0].x
        cy2 = this.con.cirList[0].y
      } else if (this.mousePos === 't') {
        cx2 = this.con.cirList[6].x
        cy2 = this.con.cirList[6].y
      } else if (this.mousePos === 'b') {
        cx2 = this.con.cirList[1].x
        cy2 = this.con.cirList[1].y
      } else if (this.mousePos === 'lm') {
        cx2 = this.con.cirList[4].x
        cy2 = this.con.cirList[4].y
      } else if (this.mousePos === 'rm') {
        cx2 = this.con.cirList[3].x
        cy2 = this.con.cirList[3].y
      }
      document.onmousemove = (e) => {
        let cx = e.clientX - disX - this.offset.left
        let cy = e.clientY - disY - this.offset.top
        // let cx2 = NaN
        // let cy2 = NaN
        // this.changeRect(cx, cy, cir.pos)
        // 计算现在鼠标所在的位置 (this.mousePos)
        // if (this.mousePos === 'lt') {
        //   cx2 = this.con.cirList[7].x
        //   cy2 = this.con.cirList[7].y
        //   this.mousePos = this.getMousePos(cx, cy, cx2, cy2) ? this.getMousePos(cx, cy, cx2, cy2) : 'lt'
        // } else if (this.mousePos === 'rt') {
        //   cx2 = this.con.cirList[5].x
        //   cy2 = this.con.cirList[5].y
        //   this.mousePos = this.getMousePos(cx, cy, cx2, cy2) ? this.getMousePos(cx, cy, cx2, cy2) : 'rt'
        // } else if (this.mousePos === 'lb') {
        //   cx2 = this.con.cirList[2].x
        //   cy2 = this.con.cirList[2].y
        //   this.mousePos = this.getMousePos(cx, cy, cx2, cy2) ? this.getMousePos(cx, cy, cx2, cy2) : 'lb'
        // } else if (this.mousePos === 'rb') {
        //   cx2 = this.con.cirList[0].x
        //   cy2 = this.con.cirList[0].y
        //   this.mousePos = this.getMousePos(cx, cy, cx2, cy2) ? this.getMousePos(cx, cy, cx2, cy2) : 'rb'
        // }
        // console.log(this.mousePos)
        // this.$emit('changeGrap', cx, cy, this.mousePos, this.con.cirList)
        this.$emit('changeGrap', cx, cy, cx2, cy2, this.mousePos)
      }
      document.onmouseup = (e) => {
        this.mousePos = ''
        document.onmousemove = null
        document.onmouseup = null
      }
    },
    getMousePos (cx, cy, cx2, cy2) {
      if (cx === cx2 || cy === cy2) {
        console.log('两点相等')
        return ''
      }
      let a = cx < cx2 ? 'l' : 'r'
      let b = cy < cy2 ? 't' : 'b'
      return [a, b].join('')
    },
    changeRect (cx, cy, pos) {
      if (pos === 'lt') {
        this.con.x = cx
        this.con.y = cy
        this.con.width = Math.abs(this.con.cirList[2].x - cx)
        this.con.height = Math.abs(this.con.cirList[5].y - cy)
        let cx2 = this.con.cirList[2].x
        let cy2 = this.con.cirList[5].y
        this.$emit('changeGrap', cx, cy, cx2, cy2)
      }
    },
    createdContour (show) {
      let obj = {}
      let x = NaN
      let y = NaN
      let xMax = NaN
      let yMax = NaN
      let width = NaN
      let height = NaN
      let conKey = [this.grap.childRef, 'con'].join('')
      for (let i = 0, l = this.grap.lrList.length; i < l; i++) { // 计算最小点和最大点
        let cx = Number(this.grap.lrList[i].style.cx)
        let cy = Number(this.grap.lrList[i].style.cy)
        if (i === 0) {
          x = cx
          y = cy
          xMax = cx
          yMax = cy
        }
        if (cx < x) {
          x = cx
        } else if (cx > xMax) {
          xMax = cx
        }
        if (cy < y) {
          y = cy
        } else if (cy > yMax) {
          yMax = cy
        }
      }
      width = xMax - x
      height = yMax - y
      obj = {
        conKey, x, y, width, height, show
      }
      obj = this.makeAnchorCir(obj)
      this.con = obj
      setTimeout(() => {
        obj.show = false
      }, 1000)
      return {
        x, y, xMax, yMax
      }
    },
    makeAnchorCir (obj) {
      let cirList = Array(8)
      cirList[0] = {
        x: obj.x,
        y: obj.y,
        cirKey: [obj.conKey, 0].join(''),
        pos: 'lt'
      }
      cirList[1] = {
        x: obj.x + obj.width / 2,
        y: obj.y,
        cirKey: [obj.conKey, 1].join(''),
        pos: 't'
      }
      cirList[2] = {
        x: obj.x + obj.width,
        y: obj.y,
        cirKey: [obj.conKey, 2].join(''),
        pos: 'rt'
      }
      cirList[3] = {
        x: obj.x,
        y: obj.y + obj.height / 2,
        cirKey: [obj.conKey, 3].join(''),
        pos: 'lm'
      }
      cirList[4] = {
        x: obj.x + obj.width,
        y: obj.y + obj.height / 2,
        cirKey: [obj.conKey, 4].join(''),
        pos: 'rm'
      }
      cirList[5] = {
        x: obj.x,
        y: obj.y + obj.height,
        cirKey: [obj.conKey, 5].join(''),
        pos: 'lb'
      }
      cirList[6] = {
        x: obj.x + obj.width / 2,
        y: obj.y + obj.height,
        cirKey: [obj.conKey, 6].join(''),
        pos: 'b'
      }
      cirList[7] = {
        x: obj.x + obj.width,
        y: obj.y + obj.height,
        cirKey: [obj.conKey, 7].join(''),
        pos: 'rb'
      }
      obj.cirList = cirList
      return obj
    }
  }
}
</script>

<style scoped>
.con-cir__ang1 {
  cursor: nw-resize;
}
.con-cir__ang2 {
  cursor: ne-resize;
}
.con-cir__side1 {
  cursor: n-resize;
}
.con-cir__side2 {
  cursor: e-resize;
}
.con-cir__ang1:hover, .con-cir__ang2:hover, .con-cir__side1:hover, .con-cir__side2:hover {
  z-index: 111111111;
}
</style>



