<template>
<div class="gomoku">
  <div class="gomoku-bg"></div>
  <div>
    <el-button type="primary" icon="el-icon-edit" circle @click="lookReplay"></el-button>
  </div>
  <div class="checkerboard checkerboard-bg" :style="{height: gridW * 16 + 'px', width: gridW * 16 + 'px'}" @click='playingChess'>
    <svg id='gomokuSvg' version="1.1" :height="gridW * 15" :width="gridW * 15" style="border: 1px solid #ccc;"
      @click.self='clearSvg'>
    <!--<svg version="1.1" height="1000" style="width:100%;border: 1px solid #ccc;">-->
      <!--画图区域-->
      <svg>
        <rect :width="gridW * 15" :height="gridW * 15" :x="this.bgLineList.xs" :y="this.bgLineList.ys"
          style="fill: #ebb88c;"/>
      </svg>
      <!--画图区域的网图-->
      <svg>
        <line v-for="ver in bgLineList.ver" :key="ver.verKey"
          :x1="ver.x1" :y1="ver.y1" :x2="ver.x2" :y2="ver.y2" :stroke='ver.stroke'
          style="stroke-width: 1"/>
        <line v-for="hor in bgLineList.hor" :key="hor.horKey"
          :x1="hor.x1" :y1="hor.y1" :x2="hor.x2" :y2="hor.y2" :stroke='hor.stroke'
          style="stroke-width: 1"/>
      </svg>
      <g v-for="con in contourList" :key="con.conKey" v-if="false">
        <rect :x="con.x" :y="con.y" :width="con.width" :height="con.height" fill="none"
          stroke="#00a8ff" stroke-dasharray="3 3" pointer-events="none"></rect>
        <g v-for="cir in con.cirList" :key="cir.cirKey">
          <image :x="cir.x" :y="cir.y" width="16" height="16" xlink:href="data:image/png;base64," preserveAspectRatio="none"></image>
        </g>
      </g>
      <circle
        v-for="(wp, index) in piece.whitePiece" v-if="piece.whitePiece.length > 0" :key="'wp' + index"
        :cx="wp[0] * gridW + gridW / 2" :cy="wp[1] * gridW + gridW / 2" :r="gridW / 2 - 2" stroke="#fff" fill="#fff" />
      <circle
        v-for="(bp, index) in piece.blackPiece" v-if="piece.blackPiece.length > 0" :key="'bp' + index"
        :cx="bp[0] * gridW + gridW / 2" :cy="bp[1] * gridW + gridW / 2" :r="gridW / 2 - 2" stroke="#333" fill="#333" />
      <!--<rect v-if="chessPos.x" :x="chessPos.x * gridW + gridW / 4" :y="chessPos.y * gridW + gridW / 4" :width="gridW / 2" :height="gridW / 2" fill="none"
        stroke="red" stroke-dasharray="3 3" pointer-events="none"></rect>-->
      <circle v-if="chessPos.x" :cx="chessPos.x * gridW + gridW / 2" :cy="chessPos.y * gridW + gridW / 2"
        :r="gridW / 2 - 1" stroke="red" fill="none"/>
      <line v-if="chessPos.x" :x1="chessPos.x * gridW + 1" :y1="chessPos.y * gridW + gridW / 2" :x2="(Number(chessPos.x) + 1) * gridW - 1"
        :y2="chessPos.y * gridW + gridW / 2" stroke-dasharray="3 3" style="stroke: red;stroke-width: 1"/>
      <line v-if="chessPos.x" :x1="chessPos.x * gridW + gridW / 2" :y1="chessPos.y * gridW + 1" :y2="(Number(chessPos.y) + 1) * gridW - 1"
        :x2="chessPos.x * gridW + gridW / 2" stroke-dasharray="3 3" style="stroke: red;stroke-width: 1"/>
    </svg>
    <div class="p_c" v-if="pText" :style="{width: gridW * 15 + 'px', height: gridW * 15 + 'px'}">
      <!--<div class="p_test" :style="{ left: t.x * gridW + 'px', top: t.y * gridW + 'px' }" v-for='(t, index) in promptTest'
        :key="'pt' + index">
        {{ t.x + '' + t.y }}
      </div>-->
      <div class="p_test" v-for='(t, index) in promptTest'
        :key="'pt' + index">
        {{ t.x + '' + t.y }}
      </div>
    </div>
    <a style="position: absolute; top: -30px; left: 140px; color: #67C23A;" target="_blank" href="https://github.com/zwkkwz1/zwkkwz1.github.io">点击进入git，欢迎来提bug</a>
    <!--<div style="position: absolute; top: -36px;" class="svg-btn__md" @click="repentChess">悔棋</div>-->
    <el-button size="mini" type="primary" plain class="huiqi" @click="repentChess">悔棋</el-button>
    <el-button size="mini" type="primary" plain class="huiqi" style="left: 50px;" @click="dbAdd()">保存</el-button>
    <!--<el-button size="mini" type="primary" plain class="huiqi" style="left: 100px;" @click="dbRead">读取</el-button>-->
  </div>
  <!--<div class="mxRubberband" v-show="rangeSel.show" :style="rangeSel.style"></div>-->
  <!--<div>
    <div v-for="(lr, index) in svgContentList.ec.lrList" :key="index"
      @mousedown.prevent="lrMd($event, lr.type, lr.style)"
      :style="lr.style" class="canvas__round"></div>
  </div>-->
  <div class="sun">
    <el-tooltip class="item" effect="dark" content="双击播放音乐" placement="bottom">
      <div class="moon" :style="moveMoon" @dblclick="playAudio" @mousedown.prevent="moveMoonMD">
        <!--<i class="el-icon-caret-right my-audio__play"></i>-->
        <i class="audio-icon" :class="{'play-icon': !palying, 'pause-icon': palying}"></i>
      </div>
    </el-tooltip>
  </div>
  <!--<img src="./ny.jpg" alt="" />-->
  <audio ref="myAudio" id='myAudio' controls style="width: 0;height: 0;" src="../../static/lrpp.mp3">
  </audio>
  <replay-table :grid='replayGrid'></replay-table>
</div>
</template>

<script>
// import myEllipse from '@/components/my-ellipse'
// import myPl from '@/components/my-pl'
import util from '@/components/lib/util'
import replayTable from './replay'
import { mapState } from 'vuex'
export default {
  components: {
    replayTable
  },
  props: {
    offset: Object
  },
  computed: {
    ...mapState({
      replays: state => state.gomokuReplay
    })
  },
  data () {
    return {
      replayGrid: {
        data: [],
        visible: false
      },
      pText: true, // 位置辅助
      palying: false,
      moveMoon: {
        left: '3px'
      },
      promptTest: [],
      gridW: 24,
      grid: 14, // 几乘几的棋盘
      pieceIndex: 0, // 黑白棋的一共下了多少子
      contourList: [], // 存放轮廓矩形
      bgLineList: { // 网格基本数据
        ver: [], // 网格竖线
        hor: [], // 网格横线
        ys: 0,
        ye: 22 * 15,
        xs: 0,
        xe: 22 * 15
      },
      chessPos: {
        x: NaN,
        y: NaN
      },
      selfOffset: {
        top: '',
        left: ''
      },
      // winLines: [], // 计入胜利手段
      // wWinLines: [],
      // bWinLines: [],
      verLines: { // 棋子的竖线
        b: [],
        w: []
      },
      piece: {
        whitePiece: [],
        blackPiece: []
      },
      pieceC: {
        whitePiece: [],
        blackPiece: []
      },
      resLen: 6
    }
  },
  mounted () {
    this.$nextTick(() => {
      // this.selfOffset.top = document.getElementById('gomokuSvg')
      this.playerI = 0
      this.chessPosCache = this.initChessPosCache()
      this.svgKey = new Date().getTime()
      for (let i = 0; i < 16; i++) { // 画网格
        let lc = i * this.gridW
        let stroke = '#905d3e'
        // if (i % 5 === 0) stroke = '#ccc'
        this.bgLineList.ver.push({
          x1: lc,
          y1: this.bgLineList.ys,
          x2: lc,
          y2: this.gridW * 15,
          verKey: this.svgKey++,
          stroke
        })
        this.bgLineList.hor.push({
          y1: lc,
          x1: this.bgLineList.xs,
          y2: lc,
          x2: this.gridW * 15,
          horKey: this.svgKey++,
          stroke
        })
      }
      this.pieceInit()
      for (let i = 0; i < 15; i++) {
        for (let j = 0; j < 15; j++) {
          let obj = {
            x: j,
            y: i
          }
          this.promptTest.push(obj)
        }
      }
      this.getPageStatus()
      window.onresize = () => {
        this.getPageStatus()
      }
      let fun = async () => {
        await this.openIndexedDB() // 打开数据库
        this.dbRead()
      }
      fun()
    })
  },
  methods: {
    lookReplay () {
      this.replayGrid = {
        data: this.replays,
        visible: true
      }
    },
    openIndexedDB () {
      return new Promise((resolve, reject) => {
        this.dbRes = window.indexedDB.open('gomoku', 6)
        let self = this
        this.dbRes.onerror = function (event) {
          self.$message({
            message: '数据库打开报错',
            type: 'error'
          })
          reject(event)
        }
        this.dbRes.onsuccess = function (event) {
          self.db = self.dbRes.result
          self.$message({
            message: '数据库打开成功',
            type: 'success'
          })
          resolve()
        }
        this.dbRes.onupgradeneeded = function (event) {
          self.db = event.target.result
          var objectStore = {}
          if (!self.db.objectStoreNames.contains('gomoku')) {
            objectStore = self.db.createObjectStore('gomoku', { autoIncrement: true })
            objectStore.createIndex('winType', 'winType', { unique: false }) // 新建索引,黑棋赢还是白棋赢
            objectStore.createIndex('saveTime', 'saveTime', { unique: false }) // 新建索引,黑棋赢还是白棋赢
          }
          resolve()
        }
      })
    },
    // 新增数据
    dbAdd (winType = '未结束') {
      let obj = {winType}
      let self = this
      obj.black = this.pieceC.blackPiece.join(';')
      obj.white = this.pieceC.whitePiece.join(';')
      obj.saveTime = util.dateToString(new Date(), 'yyyy-MM-dd hh:mm:ss')
      var request = this.db.transaction(['gomoku'], 'readwrite')
        .objectStore('gomoku')
        .add(obj)
      request.onsuccess = function (event) {
        console.log('数据写入成功')
        self.$store.commit('updateGomokuReplay', obj)
      }
      request.onerror = function (event) {
        console.log('数据写入失败')
      }
    },
    // 读取数据库
    dbRead () {
      let self = this
      var transaction = this.db.transaction(['gomoku'], 'readonly') // 新建一个事物
      var objectStore = transaction.objectStore('gomoku') // 拿到 IDBObjectStore 对象
      // 根据索引查询
      var index = objectStore.index('winType')
      var request = index.get('白棋赢')
      request.onsuccess = function (e) {
        var result = e.target.result
        if (result) {
          // ...
        } else {
          // ...
        }
      }
      // var transaction = this.db.transaction(['gomoku'])
      // var objectStore = transaction.objectStore('gomoku')
      // var request = objectStore.get(4) // 根据主键的值查询
      // request.onerror = function(event) {
      //   console.log('事务失败')
      // }
      // request.onsuccess = function( event) {
      //   if (request.result) {
      //     console.log('Name: ' + request.result.name)
      //     console.log('Age: ' + request.result.age)
      //     console.log('Email: ' + request.result.email)
      //   } else {
      //     console.log('未获得数据记录')
      //   }
      // }
      // 遍历查询
      objectStore = transaction.objectStore('gomoku')
      objectStore.openCursor().onsuccess = function (event) {
        var cursor = event.target.result
        if (cursor) {
          self.$store.commit('initGomokuReplay', cursor.value)
          cursor.continue()
        } else {
          console.log('没有更多数据了！')
        }
      }
    },
    // 判断页面高度是否让.checkerboard的 position状态是 absolute
    getPageStatus () {
      window.screenHeight = window.innerHeight
      if (window.screenHeight < 601) {
        this.isAbsolute = true // 不在date内
      } else {
        this.isAbsolute = false // 不在date内
      }
    },
    playAudio () {
      if (this.$refs.myAudio.ended || this.$refs.myAudio.paused) {
        this.$refs.myAudio.play()
        this.palying = true
      } else {
        this.$refs.myAudio.pause()
        this.palying = false
      }
    },
    setLineEnd () {
      return this.gridW * 15
    },
    // 初始化所有胜利方法的数组
    pieceInit () {
      let grid = this.grid
      this.winLines = []
      this.restWinLines = { // 记录黑白棋剩余的赢棋手段
        w: [],
        b: []
      }
      let t1 = new Date().getTime()
      this.restGrid = []
      // 所有横向赢棋手段， bWinLines， wWinLines, winLines都不在data内
      for (let i = 0; i < grid + 1; i++) {
        for (let j = 0; j < grid - 3; j++) {
          let arr = []
          for (let m = j; m < j + 5; m++) {
            let a = [m, i].join('/')
            arr.push(a)
          }
          arr.push(0)
          this.winLines.push(arr)
        }
      }
      // 所有纵向赢棋手段
      for (let i = 0; i < grid + 1; i++) {
        for (let j = 0; j < grid - 3; j++) {
          let arr = []
          for (let m = j; m < j + 5; m++) {
            let a = [i, m].join('/')
            arr.push(a)
          }
          arr.push(0)
          this.winLines.push(arr)
        }
      }
      // 所有斜向赢棋手段
      for (let i = 0; i < grid - 3; i++) { // 左上到右下斜线
        for (let j = 0; j < grid - 3; j++) {
          let arr = []
          for (let m = 0; m < 5; m++) {
            let a = [j + m, i + m].join('/')
            arr.push(a)
          }
          arr.push(0)
          this.winLines.push(arr)
        }
      }
      for (let i = grid; i > 3; i--) { // 左下到右上斜线
        for (let j = 0; j < grid - 3; j++) {
          let arr = []
          for (let m = 0; m < 5; m++) {
            let a = [j + m, i - m].join('/')
            arr.push(a)
          }
          arr.push(0)
          this.winLines.push(arr)
        }
      }
      this.restWinLines.w = []
      this.restWinLines.b = []
      // this.restWinLines.w = JSON.parse(JSON.stringify(this.winLines))
      // this.restWinLines.b = JSON.parse(JSON.stringify(this.winLines))
      let t2 = new Date().getTime()
      console.log('总共的赢棋方式', this.winLines.length, t2 - t1)
      for (let i = 0; i < grid + 1; i++) {
        for (let j = 0; j < grid + 1; j++) {
          let arr = [i, j].join('/')
          this.restGrid.push(arr) // 所有剩余可走的格子
        }
      }
    },
    initChessPosCache () {
      return {
        piece: { // 棋子走棋的位置数组
          whitePiece: [],
          blackPiece: []
        },
        pieceC: {
          whitePiece: [],
          blackPiece: []
        },
        pieceIndex: NaN,
        restWinLines: { // 黑白棋得过分的线段
          w: [],
          b: []
        },
        winLines: [], // 总赢棋方式
        restGrid: [] // 剩余位置
      }
    },
    setChessPosCache () { // 缓存， 用于悔棋
      return {
        piece: JSON.parse(JSON.stringify(this.piece)),
        pieceC: JSON.parse(JSON.stringify(this.pieceC)),
        pieceIndex: JSON.parse(JSON.stringify(this.pieceIndex)),
        restWinLines: JSON.parse(JSON.stringify(this.restWinLines)),
        winLines: JSON.parse(JSON.stringify(this.winLines)), // 总赢棋方式
        restGrid: JSON.parse(JSON.stringify(this.restGrid)) // 剩余位置
      }
    },
    repentChess () {
      this.piece = JSON.parse(JSON.stringify(this.chessPosCache.piece))
      this.pieceC = JSON.parse(JSON.stringify(this.chessPosCache.pieceC))
      this.pieceIndex = JSON.parse(JSON.stringify(this.chessPosCache.pieceIndex))
      this.restWinLines = JSON.parse(JSON.stringify(this.chessPosCache.restWinLines))
      this.winLines = JSON.parse(JSON.stringify(this.chessPosCache.winLines))
      this.restGrid = JSON.parse(JSON.stringify(this.chessPosCache.restGrid))
    },
    playingWidthPeople (e) { // 和人玩
      let type = this.pieceIndex % 2 === 0 ? 'blackPiece' : 'whitePiece'
      let mt = e.currentTarget
      const disX = e.clientX - mt.offsetLeft - this.gridW / 2
      const disY = e.clientY - mt.offsetTop - this.gridW / 2 - (this.isAbsolute ? this.offset.top : 0) + window.scrollY
      let x = Math.floor(disX / this.gridW)
      let y = Math.floor(disY / this.gridW)
      let arr = [x, y]
      this.piece[type].push(arr)
      this.pieceIndex++
    },
    playingChess (e) { // 和电脑玩
      this.step = 1
      this.playerI++
      let mt = e.currentTarget
      const disX = e.clientX - mt.offsetLeft - this.gridW / 2
      const disY = e.clientY - mt.offsetTop - this.gridW / 2 - (this.isAbsolute ? this.offset.top : 0) + window.scrollY
      let x = Math.floor(disX / this.gridW)
      let y = Math.floor(disY / this.gridW)
      if (this.playerI === 1) {
        this.chessPos.x = x
        this.chessPos.y = y
      } else if (this.playerI === 2) {
        if (this.chessPos.x === x && this.chessPos.y === y) {
          this.chessPosCache = this.setChessPosCache()
          this.playerI = 0
          this.playWidthAI(x, y)
        } else {
          this.playerI = 1
          this.chessPos.x = x
          this.chessPos.y = y
        }
      }
    },
    playWidthAI (x, y) { // 和电脑玩，人下棋
      let arr = [x, y]
      let p = [x, y].join('/')
      let index = this.restGrid.indexOf(p)
      if (index === -1) {
        return
      }
      this.restGrid.splice(index, 1)
      this.piece.blackPiece.push(arr)
      this.pieceIndex++
      this.playIn(x, y, 'b')
      this.pieceC.blackPiece.push(arr.join('/')) // 用于保存和查询所有黑子的位置
      this.aiPlay(x, y)
    },
    getScore (x, y, type) { // 毁掉多少个对手的可能赢棋手段，增加了多少自己赢棋手段
      let point = [x, y].join('/')
      let score = 0
      let ot = type === 'w' ? 'b' : 'w'
      let selfWl = this.restWinLines[type]
      let si = []
      let oi = []
      // if ((point === '9/4') || (x === '6' && y === '6') || (x === '6' && y === '7') || (x === '11' && y === '2')) {
      //   console.log(type)
      //   console.log(type)
      //   console.log(type)
      //   console.log(type)
      //   console.log(type)
      // }
      for (let i = 0, l = selfWl.length; i < l; i++) { // 增长自己得分
        let newArr = selfWl[i]
        let index = newArr.indexOf(point)
        if (index > -1) {
          if (newArr[5] === 0) {
            continue
          } else if (newArr[5] === 4) {
            var winText = type === 'w' ? '白棋胜' : '黑棋胜'
            console.log(winText)
            let sep = [type, 'win'].join('')
            return {score: 100000000, si, oi, sep}
          }
          // if (newArr[5] === 3) {
          //   score = score + Math.pow(newArr[5], newArr[5] + 1) * 2
          // } else {
          //   score = score + Math.pow(newArr[5], newArr[5] + 1)
          // }
          score += Math.pow(newArr[5], newArr[5] + 1)
          // newArr.splice(i, 1)
          si.push(i)
        }
      }
      let arr = this.restWinLines[ot]
      for (let j = 0, l = arr.length; j < l; j++) { // 阻止对方得分
        let newArr = arr[j]
        let index = newArr ? newArr.indexOf(point) : -1
        if (index > -1) {
          if (newArr[5] === 0) {
            continue
          }
          // if (newArr[5] === 3) {
          //   score = score + Math.pow(newArr[5], newArr[5]) * 2
          // } else if (newArr[5] === 4) {
          //   score = score + Math.pow(newArr[5], newArr[5]) * 3
          // } else {
          //   score = score + Math.pow(newArr[5], newArr[5])
          // }
          score += Math.pow(newArr[5], newArr[5])
          // arr.splice(j, 1)
          oi.push(j)
        }
      }
      return {score, si, oi}
    },
    playIn (x, y, type) { // 得分方式：破坏对手的可能赢棋手段，增加自己赢棋手段
      // 在这个方法中判断是否结束游戏
      let that = this
      let point = [x, y].join('/')
      let ot = type === 'w' ? 'b' : 'w'
      let num = 0
      let allL = JSON.parse(JSON.stringify(this.winLines)) // 所有win手段
      let selfWl = this.restWinLines[type] // 自己得分了的手段
      let arr = JSON.parse(JSON.stringify(this.restWinLines[ot])) // 对方得过分的手段
      for (let i = 0, l = selfWl.length; i < l; i++) { // 更新自己已有的得分手段
        let newArr = selfWl[i]
        let index = newArr.indexOf(point)
        if (index > -1) {
          newArr[5]++
        }
      }
      // 新增新的的得分手段
      allL.forEach((val, j) => {
        let index = val ? val.indexOf(point) : -1
        if (index > -1) {
          val[5]++
          selfWl.push(val) // 增加自己得分手段
          that.winLines.splice(j - num, 1) // 减少没有任何子的手段
          num++
        }
      })
      num = 0
      // for (let j = 0, l = allL.length; j < l; j++) { // 更新没有任何子的手段
      //   let newArr = allL[j]
      //   let index = newArr ? newArr.indexOf(point) : -1
      //   if (index > -1) {
      //     newArr[5]++
      //     selfWl.push(newArr) // 增加自己得分手段
      //     allL.splice(j, 1) // 减少没有任何子的手段
      //   }
      // }
      // 跟新对方的得分手段（减少）
      arr.forEach((val, m) => {
        let index = val ? val.indexOf(point) : -1
        if (index > -1) {
          that.restWinLines[ot].splice(m - num, 1)
          num++
        }
      })
      allL = []
      arr = []
      // for (let j = 0, l = arr.length; j < l; j++) { // 减少对方的得分手段
      //   let newArr = arr[j]
      //   let index = newArr ? newArr.indexOf(point) : -1
      //   if (index > -1) {
      //     arr.splice(j, 1)
      //   }
      // }
    },
    aiPlay (x, y) { // 电脑下子
      // let arr = []
      this.aiInit()
      // if (this.pieceIndex === 1) {
      //   arr = [x, y + 1]
      //   this.aiPiece(arr)
      // } else if (this.pieceIndex === 2) {
      //   arr = [x + 1, y - 1]
      //   this.aiPiece(arr)
      // } else {
      //   this.aiInit()
      // }
    },
    aiInit () { // ai初始化
      this.step = 0
      this.winLinesC = JSON.parse(JSON.stringify(this.winLines)) // 不在data内， 用于缓存
      this.restWinLinesC = JSON.parse(JSON.stringify(this.restWinLines)) // 不在data内， 用于缓存
      this.resArr = {
        lvl: 0,
        resArr: []
      } // 存放模拟数据, 不在data内
      this.disGrid = []
      this.nextStep(this.resArr)
    },
    aiPiece (arr) {
      let p = arr.join('/')
      this.chessPos.x = arr[0]
      this.chessPos.y = arr[1]
      let index = this.restGrid.indexOf(p)
      if (index === -1) {
        return
      }
      this.playIn(...arr, 'w')
      this.restGrid.splice(index, 1)
      this.piece.whitePiece.push(arr)
      this.pieceC.whitePiece.push(arr.join('/'))
    },
    getWhitePos (restGrid, type, resArr, disGrid) { // disGrid: 不允许走的点
      // TODO 缩小计算范围
      // let maxScore = 0
      let res = {}
      for (let i = 0, l = restGrid.length; i < l; i++) { // 计算第一步得分
        let rg = restGrid[i]
        if (disGrid && disGrid.indexOf(rg) > -1) {
          continue
        }
        let grid = rg.split('/')
        res = this.getScore(...grid, type) // 计算每一种走法的得分
        if (res.sep) {
          let t = type === 'w' ? '白棋胜' : '黑棋胜'
          if (this.step === 1) {
            this.aiPiece(grid)
            this.$nextTick(() => {
              this.$message(t)
              this.dbAdd(t)
            })
            return
          }
          console.log(t)
        }
        let lvl = JSON.stringify(resArr.lvl)
        let obj = {
          grid,
          score: res.score,
          resArr: [],
          sep: res.sep,
          lvl: Number(lvl) + 1
        }
        if (resArr.resArr.length === 0) {
          resArr.resArr.push(obj)
        }
        resArr.resArr = this.orderResArr(resArr.resArr, res, obj, grid)
        // TODO：后面找到的第二大得分，第三大得分， 放不进数组
        // if (this.step === 1) {
        //   if (resArr.resArr.length < this.resLen) { // 只取五个最大值
        //     resArr.resArr.push(obj)
        //   } else {
        //     resArr.resArr.splice(0, 1)
        //     resArr.resArr.push(obj)
        //   }
        // } else {
        //   resArr.resArr[0] = obj
        // }
        // maxScore = res.score
      }
      // this.aiPiece(resArr[resArr.length - 1].grid)
      this.nextStep(resArr.resArr)
    },
    orderResArr (resArr, res, obj, grid) {
      if (this.step !== 1) {
        if (res.score > resArr[resArr.length - 1].score) {
          resArr[0] = obj
        }
      } else {
        for (let i = resArr.length - 1; i > -1; i--) {
          if (res.score > resArr[i].score) {
            resArr.splice(i + 1, 0, obj)
            if (resArr.length > this.resLen) {
              resArr.splice(0, 1)
            }
            break
          }
        }
      }
      return resArr
    },
    nextStep (resArr) {
      if (this.step === 0) {
        this.step++
        this.getWhitePos(this.restGrid, 'w', resArr)
      } else if (this.step < 111111 && this.step > 0) { // 只计算到第二步
        this.step++
        console.log(this.resArr)
        // TODO： bug， disGrid不对，之前用过的点继续计算了
        for (let i = 0; i < resArr.length; i++) {
          let resC = resArr[i]
          if (resC.lvl === 1) {
            this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
            this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
            this.disGrid = []
          }
          let pType = ''
          let type = ''
          if (resC.lvl % 2 === 0) {
            pType = 'b'
            type = 'w'
          } else {
            pType = 'w'
            type = 'b'
          }
          this.playIn(...resC.grid, pType)
          this.disGrid.push(resC.grid.join('/'))
          if (resC.lvl < 10) { // 计算了接下来的8步棋
            this.getWhitePos(this.restGrid, type, resC, this.disGrid)
          }
        }
        // 计算分差
        this.isGetPos = null
        this.$nextTick(() => {
          if (!this.isGetPos) {
            this.isGetPos = this.getPos()
          }
        })
      }
    },
    getPos () {
      // let arr = []
      // let maxSc = NaN
      let is = this.resArr.resArr.length - 1
      let cbFun = (item) => {
        if (item.sep) {
          return item.sep
        } else if (item.resArr.length > 0) {
          let res = cbFun(item.resArr[0])
          return res
        } else {
          this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
          this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
          return 'safe'
        }
      }
      for (let i = is; i > -1; i--) {
        let item = this.resArr.resArr[i]
        let res = cbFun(item.resArr[0], i)
        if (res === 'bwin') {
          if (i === 0) {
            this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
            this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
            this.aiPiece(this.resArr.resArr[is].grid)
          }
          continue
        } else if (res === 'wwin' || res === 'safe') {
          this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
          this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
          this.aiPiece(item.grid)
          break
        }
        // if (item.resArr[0].sep) { // 黑棋赢
        //   if (i === 0) {
        //     this.aiPiece(this.resArr.resArr[is].grid)
        //   }
        //   continue
        // } else if (item.resArr[0].resArr[0].sep) { // 白旗赢
        //   this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
        //   this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
        //   this.aiPiece(item.grid)
        //   break
        // } else if (item.resArr[0].resArr[0].resArr[0].sep) { // 黑棋赢
        //   if (i === 0) {
        //     this.aiPiece(this.resArr.resArr[is].grid)
        //   }
        //   continue
        // } else if (item.resArr[0].resArr[0].resArr[0].resArr[0].sep) { // 白旗赢
        //   this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
        //   this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
        //   this.aiPiece(item.grid)
        //   break
        // } else if (item.resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].sep) { // 黑棋赢
        //   if (i === 0) {
        //     this.aiPiece(this.resArr.resArr[is].grid)
        //   }
        //   continue
        // } else if (item.resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].sep) { // 白旗赢
        //   this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
        //   this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
        //   this.aiPiece(item.grid)
        //   break
        // } else if (item.resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].resArr[0].sep) { // 白旗赢
        //   if (i === 0) {
        //     this.aiPiece(this.resArr.resArr[is].grid)
        //   }
        //   continue
        // } else {
        //   this.winLines = JSON.parse(JSON.stringify(this.winLinesC))
        //   this.restWinLines = JSON.parse(JSON.stringify(this.restWinLinesC))
        //   this.aiPiece(item.grid)
        //   break
        // }
      }
      return true
      // this.winLines = this.winLinesC
      // this.restWinLines = this.restWinLinesC
      // this.aiPiece(arr)
    },
    addPx (num) {
      return [num, 'px'].join('')
    },
    moveMoonMD (e) {
      // 加速度
      // const a = 2 / 100
      // let mt = this.$refs.mt
      let that = this
      let target = e.currentTarget
      const targetWidth = target.clientWidth + 50
      // const mtHeight = mt.clientHeight // 获取目标元素高宽
      const originalX = e.clientX
      const originalMoonX = Number(this.moveMoon.left.replace('px', ''))
      //    let posCache = setInterval(() => { // 每100毫秒保存一次top、left、time
      //      console.log(this.moveTool.top.substring(0, this.moveTool.top.length - 2))
      //      oldTop.push(this.moveTool.top.substring(0, this.moveTool.top.length - 2))
      //      console.log(this.moveTool.left.substring(0, this.moveTool.left.length - 2))
      //      oldLeft.push(this.moveTool.left.substring(0, this.moveTool.left.length - 2))
      //      time.push(new Date().getTime())
      //    }, interT)
      document.onmousemove = (e) => { // 鼠标按下并移动的事件
        // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let left = e.clientX - originalX
        // console.log(left, top)
        // 绑定元素位置到positionX和positionY上面
        let l = originalMoonX + left
        l = l > -targetWidth ? l > 150 ? 150 : l : -targetWidth
        that.moveMoon.left = l + 'px'
        console.log('l', l - 3)
        let vol = (147 - Math.abs(l - 3)) / 147
        let myAudio = document.getElementById('myAudio')
        myAudio.volume = vol
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
      }
    }
  }
}
</script>

